#__privileges-section {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    row-gap: 60px;
    column-gap: 30px;
    color: $black;
    width: 100%;

    @media screen and (max-width: 991px) {
        justify-content: space-between;
    }

    .thumbnail-section {
        max-width: 750px;
        width: 70%;
        @media screen and (max-width: 1399px) {
            width: 50%;
        }
        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    ul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 18px;

        li {
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}
