#faq-section-v3 {
    background-color: $soft-blue;

    .front-text-group {
        margin-bottom: 50px;
        .text-support,
        .header-primary {
            text-align: center;
        }
    }

    .subtitle {
        margin-top: 10px;
        color: $text-light;
        font-weight: 400;
        font-size: 16px;
        line-height: 32px;
    }

    .accordion {
        border: none !important;
        outline: none !important;
        width: 100%;

        &:not(:last-child) {
            margin-bottom: 20px;
        }

        @include lg {
            min-width: 500px;
            width: 500px;
        }
        &- {
            &col {
                padding: 0 !important;

                @include md {
                    &:last-child {
                        margin-top: 20px;
                    }
                }

                @include lg {
                    &:first-child {
                        padding-right: 15px !important;
                    }

                    &:last-child {
                        padding-left: 15px !important;
                        margin-top: 0;
                    }
                }
            }
            &item,
            &header,
            &button {
                border-radius: 16px;
                border: none !important;
                outline: none !important;
            }
            &button {
                -webkit-box-shadow: none !important;
                -moz-box-shadow: none !important;
                box-shadow: none !important;
                font-weight: 600;
                font-size: 16px;
                line-height: 24px;
                /* identical to box height */

                color: $black;

                &::after {
                    width: 24px;
                    height: 24px;
                    background-image: url("themes/front/images/svgs/ic_plus.svg");
                    background-size: cover;
                }
                &:not(.collapsed) {
                    &::after {
                        background-image: url("themes/front/images/svgs/ic_minus.svg");
                    }
                }
            }

            &body {
                padding-top: 0;

                p {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 28px;
                    /* or 175% */

                    color: $black;
                }
            }
        }
    }
}
