body .course-playing {
    .videos-section {
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 40px;
        padding: 0 30px;

        .section-number {
            padding: 10px 20px;
            border-radius: 999px;
            font-size: 22px !important;
            background-color: #F6F8FD;
            margin-right: 16px;
        }
    }


    .prerequisite-title {
        margin-top: 50px;
        margin-bottom: 30px;

        h6 {
            font-weight: 700;
            font-size: 20px;
            color: #34364a;
        }

        p {
            font-weight: 400;
            font-size: 16px;
            color: #34364a;
            margin-top: 6px;
        }
    }


    .prerequisite-card {
        border-radius: 16px;
        background-color: #fff;
        padding: 20px;
        box-sizing: border-box;
        margin-right: 30px;
        margin-bottom: 30px;
        width: 100%;

        img {
            border-radius: 16px;
            width: 120px;
            height: 90px;
        }

        .prerequisite-course-name {
            font-weight: 700;
            font-size: 18px;
            color: #34364a;
        }

        p {
            font-weight: 400;
            font-size: 16px;
            color: #999aa4;
            margin-top: 4px;
        }
    }

    .course-play-pills .nav-item {
        margin-right: 20px !important;

        a.nav-link {
            width: 140px;
            padding: 11px 14px !important;
            border-radius: 100px;
            background-color: #e5e9f2 !important;
            color: #34364a !important;
            text-align: center;
            text-transform: capitalize !important;

            &.active {
                background-color: #34364a !important;
                color: #e5e9f2 !important;
            }
        }
    }

    .course-play-pills a.nav-link,
    .videos-item a {
        font-weight: 500;
        font-size: 16px;
    }

    .mobile-videos {
        border-radius: 16px;
        background-color: #fff;
        color: #34364a;
        padding: 40px 0 30px;
        margin: 30px 0;

        .videos-item {
            width: 100%;
            margin-bottom: 20px;
            color: #34364a !important;
            padding: 0 30px !important;

            a {
                background-color: #F6F8FD;
                padding: 11px 20px !important;
                border-radius: 100px;
                text-decoration: none;
                display: flex;
                align-items: center;
                transition: all 0.3s;

                &.disabled {
                    background: #dedede;
                    cursor: default !important;
                }
            }
        }
    }

}

body.dark .course-playing {

    .prerequisite-title {
        margin-top: 50px;
        margin-bottom: 30px;

        h6 {
            color: #F6F8FD;
        }

        p {
            color: #F6F8FD;
        }
    }

    .prerequisite-card {
        background-color: #252635;
    }

    .videos-section .section-number {
        background-color: #1c1f2c !important;
        color: #F6F8FD;
    }

    .mobile-videos {
        .videos-item {
            a {
                background-color: #34364a;
                color: #F6F8FD;

                &:hover,
                &.active {
                    background-color: #e5e9f2 !important;
                    color: #252635 !important;
                }

                &.disabled {
                    background-color: #3c3d47;
                }
            }

            &.disabled {
                a {
                    background-color: #3c3d47 !important;
                    color: #e5e9f2 !important;
                    cursor: default !important;

                    &:hover,
                    &.active {
                        background-color: #3c3d47 !important;
                        color: #e5e9f2 !important;
                        cursor: default !important;
                    }
                }
            }
        }
    }

    .course-play-pills .nav-item {
        margin-right: 20px !important;

        a.nav-link {
            color: #e5e9f2 !important;
            background-color: #252635 !important;

            &.active {
                background-color: #e5e9f2 !important;
                color: #34364a !important;
            }
        }
    }
}


body.dark .mobile-videos,
body.dark .videos-item a.active,
.mobile-videos .videos-item a:hover,
.mobile-videos .videos-item a.active {
    color: #e5e9f2 !important;
    background-color: #252635 !important;
}

body.dark .videos-item.disabled a.active,
.mobile-videos .videos-item.disabled a:hover,
.mobile-videos .videos-item.disabled a.active {
    background-color: #3c3d47 !important;
    color: #e5e9f2 !important;
}
