#hero-section {
    padding-top: calc(100px - 24px);

    .header-title {
        font-weight: 700;
        font-size: 46px;
        line-height: 69px !important;
    }
    .subtitle {
        margin-top: 20px;
        margin-bottom: 40px;
        font-weight: 400;
        font-size: 18px;
        line-height: 32px;
    }
    .btn-free-course {
        margin-right: 14px;
    }
    .hero- {
        &label {
            font-weight: 500;
            font-size: 18px;
            line-height: 27px;
        }

        &image {
            @media screen and (min-width: 1300px) {
                padding-right: 42px;
            }
        }

        &logos {
            margin-left: -24px;
        }

        &logo {
            height: 32px;
        }

        &text {
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;

            color: $soft-grey;
        }
    }

    .progress-illustration__ {
        background-color: $white;
        border-radius: 20px;
        padding: 15px;
        color: $black;
        text-align: left !important;
        max-width: 190px;

        position: absolute;
        top: -37.5px;
        left: -49px;
        z-index: 10;

        h5.my_progress {
            font-size: 10.57px;
            font-weight: 700;
            margin-bottom: 8.55px;
        }

        .progress-item__ {
            display: flex;
            align-items: center;
            width: max-content;

            &:last-child {
                margin-top: 10.18px !important;
            }

            .logo-container {
                width: 24px;
                margin-right: 10px;
                img {
                    height: 23.62px;
                    width: 100%;
                }
            }

            .progress-detail {
                width: 100%;
                display: flex;
                flex-direction: column;
                h6 {
                    font-size: 9.47px;
                    font-weight: 600;
                    margin-bottom: 3px;
                }

                .status {
                    display: flex;
                    align-items: center;
                    gap: 7.4px;

                    span {
                        font-size: 8.27px;
                        font-weight: 500;
                        color: $dark-grey;
                    }

                    .progress-bar-container {
                        width: 106px;
                        height: 7px;
                        background-color: $soft-blue-bg;
                        border-radius: 60px;
                        position: relative;

                        .bar {
                            -webkit-transition: width 1500ms ease-in-out;
                            -moz-transition: width 1500ms ease-in-out;
                            -o-transition: width 1500ms ease-in-out;
                            transition: width 1500ms ease-in-out;
                            position: absolute;
                            z-index: 5;
                            left: 0;
                            background-color: $blue;
                            height: inherit;
                            border-radius: 60px;
                            width: 0%;
                        }
                    }
                }
            }
        }
    }

    .group-chat-illustration {
        width: 236px;
        height: 290px;
        img {
            width: 100%;
            height: auto;
        }

        position: absolute;
        bottom: -41px;
        right: 0;

        .chat-items {
            position: absolute;
            img {
                position: absolute;
                z-index: 5;
                left: 13px;

                &:first-child {
                    width: 180px;
                    top: -230px;
                }
                &:nth-child(2) {
                    width: 192px;
                    top: -192px;
                }
                &:nth-child(3) {
                    width: 220px;
                    top: -50px;
                }
            }
        }
    }
}