.select2.select2-container {
    /* position: relative; */
    border-radius: 16px !important;
    border-bottom-right-radius: 16px !important;
    width: 100% !important;
    overflow: hidden;
    &.focused {
        overflow: visible !important;
        .select2-selection {
            -webkit-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            -moz-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
        }
    }
    .selection {
        /* display: none; */
        /* border: 3px solid blue; */
    }
    .select2-selection {
        border: none;
        -webkit-border-radius: 16px;
        -moz-border-radius: 16px;
        border-radius: 16px;
        height: 45px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 15px;
        outline: none !important;
        transition: all .15s ease-in-out;
        color: #34364a;
        border: 0;
        height: 45px;
        padding: 0 12px 0 12px;
        .select2-selection__rendered {
            color: #333;
            line-height: 32px;
            padding-right: 33px;
        }
        .select2-selection__arrow {
            background: #fff;
            border: none;
            -webkit-border-radius: 16px;
            -moz-border-radius: 16px;
            border-radius: 16px;
            height: 45px;
            width: 33px;
        }
        &.select2-selection--single {
            background: #fff;
            margin-bottom: 0;
            .select2-selection__arrow {
                width: 13px;
                height: 13px;
                position: absolute;
                top: 16px;
                right: 12px;
                background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
                background-repeat: no-repeat;
                background-position: center center;
            }
        }
    }
    padding-bottom: 0;
    -webkit-box-shadow: 10px 12px 20px 0px rgba(231,
    235,
    245,
    1);
    -moz-box-shadow: 10px 12px 20px 0px rgba(231,
    235,
    245,
    1);
    box-shadow: 10px 12px 20px 0px rgba(231,
    235,
    245,
    1);
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
    border-style: solid;
    border-width: 5px 4px 0 4px;
    height: 0;
    left: 50%;
    margin-left: -4px;
    margin-top: -2px;
    position: absolute;
    top: 50%;
    width: 0;
    display: none;
}

.select2-container .select2-dropdown {
    background: transparent;
    border: none;
    margin-top: -5px;
    .select2-search {
        padding: 0;
        .select2-search__field {
            position: absolute;
            top: -50px;
            left: 0;
            /* margin-top: -150px !important; */
            outline: none !important;
            box-shadow: none;
            border: 1px solid #3498db !important;
            -webkit-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            -moz-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            border-radius: 16px;
            /* height: 40px; */
            height: 45px;
            margin: 10px 0;
            margin-bottom: 20px;
            padding-left: 18px;
            width: 0;
            opacity: 0;
            transition: all .3s;
            /* visibility: hidden; */
            &.closed {
                /* visibility: hidden; */
                width: 0;
                opacity: 0;
            }
            &.opened {
                /* visibility: visible; */
                width: 100%;
                opacity: 1;
            }
        }
    }
    .select2-results {
        padding: 0;
        border-radius: 0 0 16px 16px;
        box-shadow: 10px 12px 20px 0px #e7ebf5 !important;
        &>* {
            border-radius: 16px;
        }
        ul {
            margin-top: 10px;
            scrollbar-width: none;
            -ms-overflow-style: none;
            /* Firefox */
            &::-webkit-scrollbar {
                display: none !important;
                /* Safari and Chrome */
                width: 0;
                height: 0;
            }
            &::-webkit-scrollbar-thumb {
                background: red;
            }
            background: #fff;
            border: none;
            .select2-results__option {
                padding-left: 18px;
                border-radius: 16px;
            }
            .select2-results__option--highlighted[aria-selected] {
                background-color: #3498db;
            }
        }
    }
}