.--bwa-mobile-version {
    .success_stories {
        #review-swiper {
            display: flex;
            flex-wrap: nowrap;
        }
        .card-review {
            max-width: 320px !important;
        }
    }
    .card-review {
        padding: 12px !important;
        padding-bottom: 20px !important;
    }
    .user-avatar {
        width: 50px;
        min-width: 50px;
        max-width: fit-content;
        height: 50px;
        margin-right: 12px;
        object-fit: cover;
    }
    .username {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: $black;
    }
    .role {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: $dark-grey;
        margin-top: 0px;
    }
    p.review {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: $black;
        margin-top: 16px;
    }
}
