.revenue- {
    &timestamp {
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        color: #2447F9;
        span {
            color: #34364A;
            font-weight: 700;
        }
    }
}

.top-revenue-card {
    padding: 20px;
    border-radius: 16px;
    .revenue {
        &- {
            &table {
                margin-bottom: 0 !important;
                border-collapse: separate;
                border-spacing: 0 4px;
                tr:first-child td,
                tr:last-child td {
                    padding: 0px auto;
                }
                tr th,
                tr td {
                    padding-left: 0 !important;
                }
                tr td {
                    &.class-name {
                        /* background-color: black !important; */
                        width: 40% !important;
                    }
                    &:first-child {}
                    &:last-child {
                        text-align: right;
                    }
                }
            }
            &title {
                font-weight: 700;
                font-size: 20px;
                line-height: 30px;
                color: #34364A;
                margin-bottom: 2px;
            }
            &description {
                font-weight: 300;
                font-size: 14px;
                line-height: 21px;
                color: #999AA4;
                margin-bottom: 7px;
            }
            &number {
                width: 24px;
                height: 24px;
                background: #2447F9;
                border-radius: 50%;
                font-weight: 600;
                font-size: 14px;
                line-height: 21px;
                text-align: center;
                color: #FFFFFF;
            }
            &class-value {
                margin-bottom: 0;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #34364A;
                &.success {
                    &:after {
                        content: 'success';
                        font-weight: 700;
                        margin-left: 5px;
                    }
                }
            }
        }
    }
}