.meet-experts {
    border-radius: 16px;
    background-color: $white;
    padding: 30px;
    font-weight: 400;
    font-size: 16px;
    color: $black;

    .profile-mentor {
        img {
            width: 70px;
            object-fit: cover;
            border-radius: 999px;
        }

        .mentor-name {
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 4px;
        }

        .mentor-role {
            font-weight: 300;
            font-size: 14px;
            color: $dark-grey;
            margin-bottom: 0;
        }
    }

    .topics-list {
        display: flex;
        column-gap: 10px;
        align-items: center;

        img {
            width: 24px;
        }

        p {
            font-weight: 500;
            margin-bottom: 0;
        }
    }

    .pricing {
        color: $dark-grey;
    }
}
