.--bwa-tips {
    color: $black;

    a {
        color: $black;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        margin: 0;
        padding: 0;
    }

    .font-georgia {
        font-family: charter, Georgia, Cambria, "Times New Roman", Times, serif;
    }

    .overflow-scroll-gradient {
        position: relative;

        -webkit-mask-image: linear-gradient(to left,
                rgba(255, 255, 255, 0),
                rgba(255, 255, 255, 1) 0%);
        mask-image: linear-gradient(to left,
                rgba(255, 255, 255, 0),
                rgba(255, 255, 255, 1) 0%);

        &.is-right-overflowing {
            -webkit-mask-image: linear-gradient(to left,
                    rgba(255, 255, 255, 0),
                    rgba(255, 255, 255, 1) 30%);
            mask-image: linear-gradient(to left,
                    rgba(255, 255, 255, 0),
                    rgba(255, 255, 255, 1) 30%);
        }

        // &::after {
        //     content: '';
        //     position: absolute;
        //     right: 0;
        //     top: 0;
        //     background: linear-gradient(270deg, #FFFFFF -3.8%, rgba(255, 255, 255, 0) 113.59%);
        //     height: 45px;
        //     width: 184px;
        // }
    }

    .tips-content {
        width: 750px;
        max-width: 100%;
        position: relative;

        #tips-wrapper {
            margin-bottom: 30px;
        }

        .tipsCategory {
            margin-right: 30px;
            color: $black;
            font-weight: 400;
            font-size: 16px;
            min-width: max-content;

            &.active {
                font-weight: 600;
            }
        }

        .pagination {
            flex-wrap: wrap;
        }
    }

    .tips-item {
        display: flex;
        gap: 20px;
        align-items: center;
        padding-bottom: 30px;
        margin-bottom: 30px;
        border-bottom: 1px solid #e6e6e6;
        position: relative;

        img {
            border-radius: 18px;
            object-fit: cover;
            height: 130px;
            width: 190px;

            @media screen and (max-width: 575px) {
                width: 130px;
            }
        }

        .text-content {
            .title {
                font-size: 20px;
                font-weight: 700;
                margin-bottom: 8px;
                line-height: 30px;
            }

            .content {
                font-size: 16px;
                max-width: 440px;
            }

            .title,
            .content {
                word-break: break-word;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }
    }

    .not-available-state {
        .title {
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 8px;
        }
    }

    .side-navigation {
        width: max-content;
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 50px 50px 100px;

        @media screen and (min-width: 992px) {
            background-color: $white;
            position: fixed;
            right: 0;
            top: 0;
            height: 100vh;
            border-left: 1px solid #e6e6e6;
            padding-left: 50px;
            padding-right: 145px;
            padding-top: 100px;
        }

        @media screen and (min-width: 850px) and (max-width: 991px) {
            padding-left: 120px;
        }

        @media screen and (max-width: 575px) {
            padding-left: 16px;
        }

        img.bwa-logo {
            width: 60px;
            height: 60px;
            object-fit: cover;
            margin-bottom: 10px;
        }

        .header-thirdty {
            font-weight: 600 !important;
        }
    }

    .browse-details {
        padding-top: 50px;
        padding-bottom: 50px;

        @media screen and (min-width: 992px) {
            padding-top: 100px;
            padding-bottom: 100px;
            padding-left: 100px;
        }

        @media screen and (min-width: 1251px) {
            padding-left: 170px;
        }

        @media screen and (max-width: 991px) {
            padding-left: 50px;
            padding-right: 50px;
        }

        @media screen and (max-width: 575px) {
            padding-left: 16px;
            padding-right: 16px;
        }

        .reading-content {
            width: 100%;
            max-width: 750px;
            word-break: break-word;

            @media screen and (max-width: 991px) {
                margin: 0 auto;
            }

            @media screen and (min-width: 992px) and (max-width: 1250px) {
                max-width: 580px;
            }

            span {
                @extend .font-georgia;
            }

            img.thumbnail {
                height: 500px;
                object-fit: cover;
                border-radius: 18px;
                margin-bottom: 30px;
            }

            ol,
            ul,
            p {
                font-size: 20px;
                margin-bottom: 20px;
            }

            p {
                line-height: 34px;

                &:first-of-type {
                    margin-bottom: 30px;
                }
            }
        }

        #tips_content__container {
            img {
                border-radius: 26px;
                width: 80% !important;
                height: auto !important;
                margin-bottom: 30px;
            }
        }
    }
}
