.navy-blue-banner {
    background-color: #110843;
    position: relative;
    overflow: hidden;

    img.banner-background {
        width: 590px;
    }

    .content {
        @media screen and (max-width: 575px) {
            padding: 16px 0;
            justify-content: space-between;
            gap: 16px;
        }
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        gap: 30px;
    }

    .red-badge {
        color: #761230;
        background-color: #fd467d;
        font-weight: 600;
        font-size: 12px;
        text-align: center;
        letter-spacing: 0.015em;
        border-radius: 5px;
        padding: 1px 8px;
        line-height: 22px;
    }

    .banner-text {
        color: #ffffff;
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 0;
        @media screen and (max-width: 575px) {
            max-width: 195px;
            font-size: 14px;
            font-weight: 600;
            line-height: 21px;
            margin-top: 12px;
            span {
                font-weight: 400 !important;
            }
        }
    }

    @media screen and (max-width: 575px) {
        .btn-primary {
            padding: 8px 22px;
            font-size: 14px;
        }
    }
}

.flashsale-banner {
    position: relative;
    overflow: hidden;

    p {
        margin: 0;
        color: $black;
        line-height: 24px;
    }

    .content {
        padding: 20px;
        background-color: $white;
    }

    .banner-title {
        font-weight: 700;
        font-size: 16px;
        margin-bottom: 4px;
    }

    .banner-subtitle {
        font-weight: 400;
        font-size: 14px;
    }
}

.student-limitations-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 27px;
    padding: 18px 36px;
    background: $dark-blue;
    font-weight: 400;
    color: #000000;

    @media screen and (max-width: 500px) {
        gap: 16px;
        padding: 18px 12px;
        flex-direction: column;

        span {
            width: 75%;
        }
    }

    .badge-123 {
        border-radius: 7px;
        font-weight: 700;
        font-size: 12px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 0.015em;
        padding: 1px 6px;
        width: max-content;
    }

    .badge-sq-white {
        @extend .badge-123;
        background: #ffffff;
    }

    .badge-sq-red {
        @extend .badge-123;
        background: #f65d6d;
        color: #78141e;
    }

    .badge-sq-green {
        @extend .badge-123;
        background: $green;
        color: #000;
        // background: #20b983;
        // color: $white;
        // color: #013322;
        letter-spacing: 0.025em;
    }
}
