body .course-playing {
    aside.sidebar-aside {
        transition: all 0.3s;
        position: absolute;
        left: 0;
        margin-top: 70px;
        margin-right: 500px;
    }

    .new-sidebar {
        width: 320px;
        height: calc(100vh - 100px);
        overflow: hidden;
        position: fixed;
        z-index: 9;
        appearance: none;
        transition: all 0.3s;

        @media screen and (max-width: 992px) {
            height: calc(100vh - 10px);
            width: 100%;
            position: relative;
        }

        .wrapper-mode {
            z-index: 99;
            position: sticky;
            background-color: #fff;
            color: #34364a;
            padding: 20px;
            width: inherit;
            transition: all 0.3s;
            @extend .rounded-16px;
        }

        .sidebar-content {
            padding: 20px 0;
            z-index: 0;
            overflow-y: auto;
            appearance: none;
            height: calc(100% - 180px);

            @media screen and (max-width: 992px) {
                height: calc(100% - 100px);
            }

            &.content-no-access {
                height: calc(100% - 100px);
            }

            &::-webkit-scrollbar {
                width: 7px;
                // display: none;
            }

            &::-webkit-scrollbar-thumb {
                // display: none;
                background: #e5e9f2;
                border-radius: 10px;
            }
        }

    }

    /* START: Dark Mode Switch */
    .toggle-theme-button {
        position: relative;

        .switch {
            display: inline-block;
            position: relative;
            width: 50px;
            height: 25px;
            border-radius: 20px;
            background: #dfd9ea;
            transition: background 0.28s cubic-bezier(0.4, 0, 0.2, 1);
            vertical-align: middle;
            cursor: pointer;

            &::before {
                content: "";
                position: absolute;
                top: 1px;
                left: 2px;
                width: 22px;
                height: 22px;
                background: #fafafa;
                border-radius: 50%;
                transition: left 0.28s cubic-bezier(0.4, 0, 0.2, 1),
                    background 0.28s cubic-bezier(0.4, 0, 0.2, 1),
                    box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
            }

            &:active::before {
                box-shadow: 0 2px 8px rgba(0, 0, 0, 0.28),
                    0 0 0 20px rgba(128, 128, 128, 0.1);
            }
        }

        input#toggle-color-mode {
            width: 50px;
            height: 25px;
            position: absolute;
            z-index: 10;
            opacity: 0;
            cursor: pointer;

            &:checked+.switch {
                background: #72da67;
            }

            &:checked+.switch::before {
                left: 27px;
                background: #fff;
            }

            &:checked+.switch:active::before {
                box-shadow: 0 2px 8px rgba(0, 0, 0, 0.28), 0 0 0 20px rgba(0, 150, 136, 0.2);
            }
        }
    }

    /* END: Dark Mode Switch */

}

// Dark Mode
body.dark .course-playing {
    .new-sidebar {
        // background-color: #252635;
        border: 0;

        .wrapper-mode {
            color: $white;
            background-color: #252635;
            border: 0;
        }
    }
}

@import 'video-list';
