#tutorials_page {
    .front-text-group {
        margin-bottom: 24px;
    }

    .cards-row {
        row-gap: 30px;
    }

    .card-kelas_saya {
        padding: 16px;
        min-height: 299px;

        .name {
            @media (min-width: 0px) and (max-width: 576px) {
                display: -webkit-box !important;
                -webkit-line-clamp: 2 !important;
                overflow: hidden !important;
            }

            margin-top: 16px;
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;
        }

        .role {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            margin-top: 6px;
        }

        .thumbnail_kelas {
            width: 100%;
            border-radius: 16px !important;

            @include md {
                height: 170px;
            }

            object-fit: cover;
        }

        .detail_card {
            min-height: 97px;
        }
    }
}
