.product-card-responsive {
    position: relative;
    padding: 30px;
    border-radius: 14px;
    border: none;
    background: $white;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    row-gap: 24px;
    color: $black;
    height: 100%;

    @media screen and (max-width: 500px) {
        padding: 0;
        row-gap: 16px;
        border-radius: 14px;
        flex-direction: row;
    }

    .thumbnail-product {
        border-radius: 16px;
        object-fit: cover;
        width: 100%;
        max-height: 210px;

        @media screen and (max-width: 500px) {
            border-top-left-radius: 16px;
            border-bottom-left-radius: 16px;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            width: 110px;
            height: 100%;
        }
    }

    .product-detail {
        @media screen and (max-width: 500px) {
            padding: 14px;
        }

        .product-name {
            font-weight: 700 !important;
            font-size: 20px;
            min-height: 60px;

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;

            &:hover {
                -webkit-line-clamp: normal;
                overflow: visible;
                display: block;
            }

            @media (max-width: 991px) {
                -webkit-line-clamp: normal !important;
                overflow: visible;
                display: block;
            }

            @media (max-width: 500px) {
                -webkit-line-clamp: 3 !important;
                display: -webkit-box;
                overflow: hidden;
                font-size: 16px;
                line-height: 26px;
                min-height: 50px;
            }
        }

        @media screen and (max-width: 500px) {
            .btn {
                font-size: 14px !important;
            }
        }

        // .product-price {
        //     font-size: 16px;

        //     @media (max-width: 500px) {
        //         font-size: 14px;
        //         color: $dark-grey;
        //         font-weight: 300;
        //     }
        // }
    }
}
