#bannerAdminModeView {
    background-color: $dark-ocean-blue;
    padding: 23px 0;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;

    @media screen and (max-width: 575px) {
        bottom: 79px;
    }

    .container > div {
        padding: 0 48px;
        @media screen and (max-width: 991px) {
            padding: 0 12px;
        }
    }

    .text-white {
        p {
            margin-bottom: 6px;
            font-size: 14px;
        }

        h6 {
            font-size: 20px;
            font-weight: 600;
        }
    }
}
