@keyframes move {
    0% {
        top: 70px;
    }
    50% {
        top: -55px;
    }
    100% {
        top: 70px;
    }
}
@keyframes move-down {
    0% {
        bottom: 70px;
    }
    50% {
        bottom: -55px;
    }
    100% {
        bottom: 70px;
    }
}

#testimonial-section-v3 {
    background-color: $purple;
    overflow: hidden;
    margin: 50px 0 100px 0;
    // height: 900px;
    height: auto;
    @include lg {
        height: 600px;
    }
    .header-title {
        margin-top: 10px;
        font-weight: 700;
        font-size: 32px;
        line-height: 48px !important;
    }
    .subtitle {
        margin-top: 10px;
        color: $text-light;
        font-weight: 400;
        font-size: 16px;
        line-height: 32px;
    }
    .btn-showcase {
        background-color: $white;
        color: $black;
        border-radius: 100px;
    }
    .btn-testimonial {
        padding: 0;
        width: 170px !important;
        height: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .testimonial- {
        &row {
            // padding: 50px 0;
            // overflow: hidden;
            // column-gap: 30px;
        }
        &column {
            position: relative;
            -ms-overflow-style: none;
             /* IE and Edge */
             scrollbar-width: none;
             /* Firefox */
             &::-webkit-scrollbar {
                 display: none;
             }
            overflow-y: scroll;

            &:first-child {
                animation: move 15s ease-in infinite;
                top: 50px;
            }
            &:last-child {
                animation: move-down 15s ease-in infinite;
                bottom: 50px;
                margin-left: 30px;
            }
        }
        &card {
            & > * {
                text-align: left !important;
            }
            width: 330px;
            background: $white;
            border-radius: 16px;
            padding: 20px;
            &:not(:last-child) {
                margin-bottom: 30px;
                // display: none;
            }
        }
        &title {
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;
            color: $black;
        }
        &description {
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: $black;
            margin-top: 10px;
            margin-bottom: 20px;
            width: max-content;
        }
        &profile {
            width: 36px;
            height: 36px;
            margin-right: 10px;
        }
        &author {
            font-weight: 700;
            font-size: 12px;
            line-height: 18px;
            color: $black;
        }
        &role {
            font-weight: 300;
            font-size: 12px;
            line-height: 18px;
            color: $dark-grey;
        }
    }
}