.__mobile-checkout {
    padding-bottom: 36px;

    .form-control {
        border-radius: 999px;
        border: 1px solid $dark-blue;
        padding: 8px 16px;
        color: $black;
        font-weight: 500;
        font-size: 14px;
        background-color: $white;

        &::placeholder {
            color: $dark-grey;
            font-weight: 400;
            opacity: 1;
        }

        &:-ms-input-placeholder {
            color: $dark-grey;
            font-weight: 400;
        }

        &::-ms-input-placeholder {
            color: $dark-grey;
            font-weight: 400;
        }
    }

    .thumbnailContainer {
        max-width: 120px;
        width: 100%;
        height: 86px;
        overflow: hidden;

        ._btn-play {
            width: 30px;
        }

        .thumbnail-course {
            border-radius: 16px;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    .promo_code_container {
        margin: 16px 0;
        padding: 16px 0;
        position: relative;
    }

    .h-form-group {
        border: 1px solid $dark-blue;
        -webkit-border: 1px solid $dark-blue;
        -moz-border: 1px solid $dark-blue;
        border-radius: 999px;
        padding: 8px 0 8px 16px;
        font-size: 14px;
        width: 100%;
        position: relative;

        input,
        input:focus {
            background-color: transparent;
            border: 0;
            -webkit-border: 0;
            -moz-border: 0;
            outline: 0;
            color: $black;
            font-weight: 500;
            width: 75%;
        }

        .btn-secondary {
            padding: 6px 24px;
            max-width: 90px;
            width: 100%;
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            font-size: 14px;
        }
    }

    .icon_placeholder {
        margin-left: 7px;

        border-radius: 999px;
        width: 24px;
        height: 22px;
        background: $dark-blue;
        display: block;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
            vertical-align: initial;
        }
    }

    .card_checkout_details {
        padding: 16px 16px 20px;
        background-color: $white;
        border-radius: 16px;
        overflow: hidden;
        margin-bottom: 20px;

        &#mobile-upload-proof {
            padding-bottom: 16px !important;
        }

        .title {
            font-weight: 700;
            font-size: 16px;
            margin-bottom: 10px;
        }

        .details {
            display: flex;
            flex-direction: column;
            gap: 8px;
            width: 100%;

            .item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-size: 14px;
            }
        }
    }

    .item-bank {
        margin-top: 10px;
        font-size: 14px;
        font-weight: 600;

        img {
            height: 28px;
            object-fit: cover;
            margin-bottom: 8px;
        }

        .info {
            display: flex;
            flex-direction: column;
            gap: 4px;
        }
    }

    .nav-tabs {
        button.mobile_card {
            width: 48%;
            border: 2px solid transparent;
            -webkit-border: 2px solid transparent;
            -moz-border: 2px solid transparent;
        }
    }

    .placeholder-dark {
        font-weight: 500;
        font-size: 16px !important;
        line-height: 24px;
        color: #34364a !important;
    }

    // ::-webkit-input-placeholder {
    //     @extend .placeholder-dark;
    // }

    .proof-wrapper {
        .form-label {
            font-size: 14px;
            line-height: 21px;
            margin-bottom: 6px;
        }

        &.telegram {
            margin-top: 12px;
        }

        .custom-file-wrapper {
            border: 1px solid $base-grey;
            border-radius: 100px;

            &.active {
                -webkit-box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
                -moz-box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
                box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
            }

            &.telegram {
                color: $black !important;

                .form-control {
                    border: none !important;
                    padding: 0 !important;
                    height: 36px;
                    border-radius: 0 999px 999px 0 !important;

                    &:focus {
                        box-shadow: none !important;
                        outline: none !important;
                    }
                }

                .telegram-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-left: 16px;
                    margin-right: 11px;
                    height: 100%;
                }
            }
        }
    }

    .custom-file- {
        &input {
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-left: 43px;

            font-weight: 400;
            font-size: 14px;
            line-height: 18px;
            /* identical to box height */

            color: $dark-grey !important;
            min-height: 36px;

            &::-webkit-input-placeholder {
                color: $dark-grey !important;
                height: 36px;
            }

            &:hover {
                cursor: pointer;
            }

            &.attachment {
                &::before {
                    content: url("/themes/front/images/svgs/ic_clip_blue.svg");
                    background: transparent;
                    outline: none;
                    white-space: nowrap;
                    user-select: none;
                    -webkit-user-select: none;
                    cursor: pointer;
                    position: absolute;
                    top: 8px;
                    bottom: 11px;
                    left: 15px;
                }

                &::-webkit-file-upload-button {
                    width: 43px;
                    visibility: hidden;
                }
            }
        }
    }

    .img-checkout {
        margin-bottom: 70px !important;
    }

    .header-primary {
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
    }

    .subtitle-primary {
        font-size: 14px;
        line-height: 21px;
        margin-top: 6px;
        margin-bottom: 30px;
    }

    .btn-transaction {
        font-weight: 600;
        min-width: fit-content !important;
        max-width: 200px;
    }
}
