.progress-belajar {
    .flickity-viewport {
        height: 180px !important;
    }

    padding-right: 50px;
    padding-left: 50px;

    .progress-belajar-card {
        overflow-x: auto !important;
        overflow-y: visible;
        column-gap: 30px;
        width: 100%;

        .card-progress {
            border-radius: 16px;
            background-color: $white;
            box-sizing: border-box;
            padding: 20px;
            width: 530px !important;
            height: 170px;
            position: relative;
            margin-right: 30px;

            img.thumbnail-img {
                border-radius: 16px;
                object-fit: cover;
                width: 200px;
                height: 130px;
                margin-right: 14px;
            }

            img.icon-level {
                cursor: pointer;
                z-index: 999;
            }

            .title {
                font-size: 20px;
                font-weight: 700;
                color: $black;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }

            p {
                color: $dark-grey;
                font-size: 16px;
                width: 100%;
            }

            p.course-type {
                font-size: 16px;
                font-weight: 300 !important;
                margin-top: 10px;
            }

            .total_done_materi {
                font-size: 12px;
                color: $dark-grey;
                font-weight: 400;
                width: max-content;
                // margin-left: 11px;
            }

            .__progress-belajar-bar {
                width: 75%;
                height: 6px;
                background-color: $dark-blue;
                border-radius: 50px;
                overflow: hidden;

                .progress-bar {
                    height: 100%;
                    border-radius: 50px;
                    background-color: $blue;
                }
            }
        }
    }
}

@media (max-width: 500px) {
    .progress-belajar {
        padding-right: 12px;
        padding-left: 12px;

        .progress-belajar-card {
            overflow-x: auto !important;
            column-gap: 30px;
            width: 100%;

            .card-progress {
                width: 85% !important;
                margin-right: 16px;

                // .__progress-belajar-bar {
                //     width: 140px !important;
                // }
            }
        }
    }
}

#progress_belajar {
    @media screen and (max-width: 576px) {
        padding-top: 0 !important;

        .tagline {
            margin-top: 0;
        }

        .default-card {
            background-color: $white;

            &.chart-card {
                padding: 0;
                .col-12 {
                    padding: 20px 28px;
                }
            }
            &.progress-card {
                padding: 0;
                background-color: transparent;
            }
            .item {
                background-color: $white;
                border-radius: 16px;
                padding: 12px;
                padding-right: 40px;
                margin-bottom: 16px;

                .col-auto {
                    &:first-child {
                        padding-right: 0;
                    }
                    &:last-child {
                        padding-left: 0;
                    }
                }

                .thumbnail {
                    height: 60px;
                    width: 78px;
                    object-fit: cover;
                    margin-top: 0;
                    border-radius: 14px;
                }

                .lh-base {
                    font-weight: 700;
                    font-size: 14px;
                    line-height: 21px;

                    color: $black;
                    width: calc(100vw - 78px - 24px - 16px - 38px) !important;
                }

                .course {
                    width: calc(100vw - 78px - 24px - 11px) !important;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: flex-start;
                    height: 100%;
                }

                .progress-group {
                    width: calc(100% - 12px - 11px);
                    column-gap: 10px;
                }

                .progress {
                    height: 6px;
                }
                .total-progress {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 18px;

                    color: $dark-grey;
                }
            }
        }
    }
}
