.__detail-kelas {
    padding-bottom: 110px;

    .__PillsBody {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .paragraph_about {
        position: relative;
        line-height: 26px;

        .read_more_less {
            position: absolute;
            text-decoration: underline;
            color: $primary-blue;
            font-size: 14px;
            // background-color: $soft-blue;
            background: linear-gradient(
                270deg,
                #f6f8fd 95%,
                rgba(246, 248, 253, 0) 100%
            );
            right: 0;
            bottom: 0;
            padding-left: 8px;
            cursor: pointer;
        }
    }

    .line-clamp-4 {
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    // read_more helper for hide content
    .height-104 {
        max-height: 104px;
        overflow: hidden;
    }

    .learn-with-expert {
        .mobile_card {
            min-width: fit-content;
            max-width: 250px;
            width: 100%;

            .primary-text {
                max-width: 132px;
            }
        }
    }

    .__main-preview {
        width: 100%;
        height: auto;
        max-height: 250px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .__carousel-preview {
        padding: 12px 16px;
        padding-right: 0;
        background-color: $white;

        img {
            border-radius: 14px;
            border: 2px solid transparent;
            outline: none;
            width: 78px;
            height: 60px;
            object-fit: cover;
            overflow: hidden;
            margin-right: 10px;
            transition: all 0.1s ease-out;

            &.is-selected.is-nav-selected {
                border: 2px solid $primary-blue;
                -webkit-border: 2px solid $primary-blue;
                -moz-border: 2px solid $primary-blue;
            }
        }
    }

    .card-review {
        .d-none {
            display: block !important;
        }
    }
}
