#mentorDashboard {
    .rcDescription_datatable {
        cursor: pointer;
    }

    .course_detail:hover {
        color: blue;
        font-style: italic;
    }

    .course_detail:hover:after {
        content: attr(data-restOfDesc);
        color: blue;
        font-style: italic;
    }

    .badge-blue,
    .badge-yellow,
    .badge-red,
    .badge-grey {
        font-size: 14px;
        padding: 4px 16px;
    }

    table.datatable-Transaction tbody tr td {
        vertical-align: middle;
    }

    .ranges li {
        background-color: #e5e9f2 !important;
        border: 1px solid #e5e9f2 !important;
        color: #34364a !important;
    }

    .ranges li.active {
        background-color: #2447f9 !important;
        border: 1px solid #2447f9 !important;
        color: #fff !important;
    }

    .daterangepicker td.active,
    .daterangepicker td.active:hover {
        background-color: #2447f9 !important;
        color: #fff !important;
    }

    .range_inputs {
        display: grid;
        gap: 8px;
    }

    .btn-success:disabled {
        color: #fff;
        background-color: #2e98f0;
        border-color: #2e98f0;
    }

    .btn-success,
    .btn-success:hover {
        color: #fff;
        background-color: #2447f9;
        border-color: #2447f9;
    }

    @media screen and (max-width: 500px) {
        div.dataTables_wrapper div.dataTables_filter input {
            width: 100%;
            max-width: 200px;
            margin-top: 20px;
            margin-bottom: 30px;
        }
    }

    .tippy-box[data-theme~="tipAdminFee"],
    .tippy-tooltip.tipAdminFee-theme {
        .tippy-backdrop {
            background-color: $white;
        }
        background-color: $white;
        box-shadow: 0px 12px 25px #f1f2f6;
        -webkit-box-shadow: 0px 12px 25px #f1f2f6;
        -moz-box-shadow: 0px 12px 25px #f1f2f6;
        box-shadow: 0px 12px 25px #f1f2f6;
        border-radius: 17px;
        border: 1px solid #ebedf1;
        padding: 15px !important;
        color: $black;
        font-size: 12px;
        line-height: 18px;

        .tippy-content {
            padding: 0 !important;
        }
    }

}

//new for datatable
.mentor-datatables{
    
    table.dataTable thead th {
        padding: 16px;
    }
    table.dataTable td {
        font-weight: 500 !important;
        padding: 16px;
    }
    table.table-bordered.dataTable tbody tr > td {
        border-bottom-width: 0px;
    }
    table.table-bordered.dataTable tbody tr:last-of-type > td {
        border-bottom-width: 1px;
    }
    div.dataTables_wrapper div.dataTables_length {
        color: #34364a !important;
    }
    div.dataTables_wrapper div.dataTables_filter label {
        color: #34364a !important;
    }
    div.dataTables_wrapper div.dataTables_length select {
        font-weight: 900 !important;
        background-color: transparent;
    }
    div.dataTables_wrapper div.dataTables_info {
        color: #999aa4 !important;
    }
    div.dataTables_wrapper
        div.dataTables_paginate
        ul.pagination
        li.paginate_button
        a.page-link {
        color: #2447f9 !important;
    }
    div.dataTables_wrapper
        div.dataTables_paginate
        ul.pagination
        li.paginate_button.active
        a.page-link {
        color: #fff !important;
    }
    .dataTables_scroll {
        border-block: 1px;
        margin-block: 20px;
    }
    .pagination .page-item {
        margin-left: 10px;
        padding: 0;
        .page-link {
            width: 38px !important;
            height: 38px !important;
        }
    }
    div.dataTables_wrapper
        div.dataTables_paginate
        ul.pagination
        li.paginate_button
        a.page-link {
        color: #34364a !important;
        font-weight: 500;
    }
    .pagination {
        .page-item:not(.active) .page-link {
            border: 1px solid #d7dbe1;
        }
        .page-item.disabled .page-link {
            border: none;
        }
    }
}
