.--bwa-mobile-version #offcanvasSearchCourse {
    .offcanvas-header {
        padding: 30px 20px 0;
    }

    .offcanvas-body {
        padding: 30px 20px;
    }

    .offcanvas-form-group {
        padding-bottom: 16px;
        border-bottom: 1.5px dashed $dark-blue;
        position: relative;

        input {
            width: 89% !important;
        }

        .btn-basic {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            z-index: 10;
            padding-bottom: 16px;
            background-color: #fff;
        }
    }

    #loadMoreCourses {
        .btn-secondary:hover {
            box-shadow: none;
        }
    }

    .offcanvas-form-control {
        padding: 0;
        border-radius: 0;
        border: 0;
        background-color: transparent;
        font-weight: 400;
        width: 100%;

        &:focus {
            outline: none;
        }
    }

    .most-searched-course .course-content .course-title,
    .tt-menu .course-content .course-title {
        max-height: 48px;
        overflow: hidden;
    }

    .most-searched-course {
        top: auto;
        position: relative;
        z-index: 0;
        max-height: 65vh;

        .course-content {
            position: relative;
            border-bottom: none;
            gap: 12px;
        }
    }

    .tt-menu,
    .most-searched-course {
        .course-content {
            padding: 7px 0 !important;

            .thumbnail {
                width: 90px;
                height: 70px;

                img {
                    border-radius: 14px;
                }
            }
        }
    }
}
