.--bwa-mobile-version {
    .btn-basic {
        background-color: transparent;
        outline: none;
        border: none;
    }
    .mobile {
        &- {
            &title {
                font-weight: 700;
                font-size: 24px;
                line-height: 36px;
                color: $black;
            }
            &sub-title {
                font-weight: 700;
                font-size: 16px;
                line-height: 24px;
                margin-top: 20px;
                color: $black;
                margin-bottom: 6px;
            }
            &description {
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                color: $black;
            }
            &search-bar {
                /* box-shadow: -webkit-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            -moz-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
            box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important; */
                input {
                    /* box-shadow: none !important; */
                    background-color: $white;
                }
                #button-addon2 {
                    /* -webkit-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
                -moz-box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important;
                box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25) !important; */
                    background-color: $white !important;
                    border-radius: 0 100px 100px 0;
                    padding: 0 15px !important;
                }
                &::placeholder {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: $dark-grey;
                }
            }
            &section {
                &- {
                    &header {
                        margin-bottom: 16px;
                    }
                    &label {
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 21px;
                        color: $green;
                    }
                    &title {
                        font-weight: 700;
                        font-size: 24px;
                        line-height: 36px;
                        color: $black;
                        margin-bottom: 16px;
                        &.title-exception {
                            margin-bottom: 0px !important;
                        }
                    }
                }
            }
            &footer {
                &- {
                    &brand {
                        font-weight: 700;
                        font-size: 16px;
                        color: $black;
                        margin-bottom: 2px;
                    }
                    &copyright {
                        font-weight: 400;
                        font-size: 14px;
                        color: $dark-grey;
                    }
                    &description {
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 21px;
                        color: $black;
                        margin-top: 12px;
                        margin-bottom: 20px;
                    }
                    &sub {
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 24px;
                        color: $black;
                        margin-bottom: 12px;
                    }
                    &item {
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 21px;
                        color: $black;
                    }
                }
            }
        }
    }
    .expanding-search {
        &- {
            &bar {
                max-width: calc(100vw - 24px) !important;
                &.active {
                    display: flex !important;
                    .expanding-search-icon {
                        // left: 100%;
                        right: 0px !important;
                    }
                }
            }
            &icon {
                // left: 50%;
                // top: 50%;
                // transform: translate(-50%, -50%);
                right: 0px;
                top: 0px;
                bottom: 0px;
                // width: 64px;

                padding: 0;
                border-radius: 0 100px 100px 0;
                border: none;
                outline: none;

                img {
                    margin-right: 20px;
                }
            }
            &input {
                position: relative;
                // left: 24px;
                // right: 55px;

                input {
                    padding-left: 20px !important;

                    &:-ms-input-placeholder {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: $dark-grey;
                    }

                    &::-ms-input-placeholder {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: $dark-grey;
                    }

                    &::placeholder {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 24px;
                        color: $dark-grey;
                    }
                }
            }
        }
    }

    .transaction- {
        &label {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: $black;
        }
        &value {
            font-weight: 600;
            font-size: 14px;
            line-height: 21px;

            color: $black;
        }

        &info {
            padding: 6px 20px 12px 12px;
            border-radius: 0 0 16px 16px;
            row-gap: 8px;
        }
    }
}
