.card-kelas_saya {
    padding: 24px;
    background: #fff;
    border-radius: 14px;
    position: relative;
    overflow: hidden;
    color: $black;

    .thumbnail_kelas {
        border-radius: 16px;
        max-width: 100%;
        width: 100%;
        object-fit: cover;
    }

    .name {
        font-size: 20px;
        margin-bottom: 0;
        font-weight: 700;
        text-transform: capitalize;
        margin-top: 24px;
    }

    .role {
        font-weight: 300;
        font-size: 16px;
        color: $dark-grey;
        margin-top: 4px;
    }

    .status_kelas {
        font-weight: 500;
    }

    .detail_card {
        .stretched-link {
            z-index: 1;

            &:hover {
                cursor: pointer;
            }
        }

        .name {
            z-index: 2;

            &:hover {
                cursor: pointer;
            }
        }
    }

    @media screen and (max-width: 500px) {
        padding: 0;
        display: flex;
        flex-direction: row;

        .detail_card {
            padding: 14px 1px 14px 14px;
        }

        .thumbnail_kelas {
            border-radius: 0 !important;
            border-top-left-radius: 16px;
            border-bottom-left-radius: 16px;
            width: 110px;
            height: auto;
        }

        .name {
            color: $black;
            font-size: 16px;
            line-height: 26px;
            margin-top: 0;
            margin-bottom: 10px;
            min-height: 50px;
        }

        .icon {
            width: 20px;
            height: 20px;
        }

        .status_kelas {
            font-weight: 300;
            color: $dark-grey;
            font-size: 14px;
        }

        .btn-primary,
        .btn-secondary {
            font-size: 14px !important;
        }
    }
}
