.cardSlim {
    background-color: $white;
    border: 0;
    border-radius: 16px;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 16px;
    padding: 20px;
    transition: all 0.3s ease-in-out;
    position: relative;

    &:hover,
    &:focus,
    &:focus-visible {
        box-shadow: 0 0 0 2px $blue inset;
    }

    &::before {
        display: block;
    }

    img {
        height: auto;
        max-width: 70px;
        max-height: 70px;
        width: 100%;
    }

    .title {
        font-size: 20px;
        font-weight: 700;
        line-height: 30px;
        color: $black !important;
        margin: 0;
        text-transform: capitalize;
    }

    .subtitle {
        font-size: 14px;
        font-weight: 300;
        color: $dark-grey;
        margin: 4px 0 0;
    }
}
