.--bwa-mobile-version {
    #advice-mobile {
        margin-top: 30px;
        .wrapperBorder {
            position: relative;
            display: flex;
            flex-direction: column;
            gap: 16px;
        }
        .advice-card {
            background: $white;
            border-radius: 16px;
            padding: 12px;
            &-border {
                width: 105%;
                height: 1px;
                background-image: linear-gradient(
                    to right,
                    $base-grey 0%,
                    $base-grey 50%,
                    transparent 50%
                );
                background-size: 12px 1px;
                background-repeat: repeat-x;
                margin-left: -3px;
                /* padding-top: 16px;
                padding-bottom: 16px; */
                &:first-child {
                    margin-bottom: 16px;
                }
                &:last-child {
                    margin-top: 16px;
                }
            }
            .thumbnail {
                width: 50px;
                height: 50px;
                margin-right: 12px;
            }
            .title {
                font-weight: 700;
                font-size: 16px;
                line-height: 24px;
                color: $black;
                margin-bottom: 0;
            }
            .sub {
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                color: $dark-grey;
                margin-bottom: 0;
            }
            .link {
                font-weight: 500;
                font-size: 14px;
                line-height: 24px;
                /* identical to box height, or 171% */
                text-align: center;
                color: $black;
                width: 90px;
                height: 36px;
                background: $base-grey;
                border-radius: 100px;
            }
        }
    }
}
