.--bwa-mobile-version {
    #unlock_better_version {
        margin-bottom: 40px;
        .logo {
            margin-bottom: 16px;
        }
    }
    .start-building-col {
        display: flex;
        gap: 13px;
        align-items: center;
        margin-top: 20px;
        a.btn {
            font-weight: 500;
            font-size: 14px;
            padding: 6px 14px;
            line-height: 24px;
            width: 48%;
        }
        @media screen and (max-width: 370px) {
            flex-direction: column;
            a.btn {
                width: 100% !important;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}
