body .course-playing {
    .section-video-list {
        padding-bottom: 30px;
        padding-left: 30px;
        padding-right: 30px;

        &:last-of-type {
            padding-bottom: 0;
        }

        .section-heading-video {
            font-weight: 700;
            font-size: 16px;
            margin-bottom: 20px;
        }

        .item-video {
            position: relative;
            width: 100%;
            border-radius: 100px;
            background: #e5e9f2;
            color: #34364a;
            padding: 11px 22px;
            text-decoration: none;
            cursor: pointer !important;
            margin-bottom: 16px;
            transition-duration: 0.3s;

            span {
                font-weight: 600;
                font-size: 14px;
                text-transform: capitalize;
                width: 100%;
                position: relative;
            }

            &.disabled {
                background: #dedede;
                cursor: default !important;
            }

            &:hover,
            &.active {
                color: #f6f8fd;
                background-color: #34364a;
            }

            &:hover,
            &.active {
                span {
                    -webkit-line-clamp: normal;
                    overflow: visible;
                    display: block;
                }
            }

            &.disabled:hover {
                background-color: #dedede;
                cursor: not-allowed !important;
                color: #34364a;
            }

            i.materi-check-done {
                color: #22c58b;
            }
        }
    }

    .accordion {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        transition: all 0.3s;

        .accordion-item.section-video-list {
            border: 0 !important;
            background-color: $white;
            padding: 0 !important;
            transition: all 0.3s;
            @extend .rounded-16px;

            button.accordion-button {
                @extend .rounded-16px;
                transition: all 0.3s !important;
                padding: 20px;
                background-color: transparent;

                .section-heading-video {
                    margin-bottom: 0 !important;
                }

                &:focus,
                &:not(.collapsed) {
                    color: initial;
                    background-color: initial;
                    box-shadow: none;
                    transition: all 0.3s !important;
                    border: none;
                }

                &:not(.collapsed)::after,
                &::after {
                    background-image: none;
                }

                &::after {
                    background-position: center;
                    width: 24px;
                    height: 24px;
                    border-radius: 100px;
                    background-color: $dark-blue;
                    background-size: 14px;
                }
            }

            .accordion-body {
                padding: 0 22px 20px;
                display: flex;
                flex-direction: column;
                gap: 16px;
                transition: all 0.3s;

                .item-video {
                    margin-bottom: 0;
                }
            }
        }

        .accordion-item.section-video-list
            button.accordion-button:not(.collapsed)::after,
        .accordion-item.section-video-list button.accordion-button::after {
            background-image: url("/themes/front/images/svgs/ic_arrow-vertical.svg");
        }
    }
}

body.dark .course-playing {
    .section-video-list .item-video {
        background-color: #34364a;

        &.disabled {
            background-color: #3c3d47;

            &:hover,
            &.active {
                background-color: #3c3d47 !important;
                color: #e5e9f2 !important;
                cursor: default !important;
            }
        }

        &:hover,
        &.active {
            background-color: #e5e9f2 !important;
            color: #252635 !important;
        }
    }

    .accordion {
        .accordion-item.section-video-list {
            background: #252635;
        }
    }
}
