.--bwa-mobile-version {
    .course-categories {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        width: 100%;
        .row {
            row-gap: 14px;
            margin: 0 !important;
        }

        .item-category.mobile {
            // margin-bottom: 14px;
            padding: 12px !important;
            width: 48%;
            height: fit-content;
            img {
                width: 50px;
                height: 50px;
            }
            .footer {
                margin-top: 11px;
            }
            .info {
                min-height: auto;
                .title {
                    font-size: 16px !important;
                    line-height: 24px;
                    color: $black;
                    margin-bottom: 2px;
                }
                .sub {
                    font-weight: 400;
                    font-size: 14px !important;
                    line-height: 21px;
                    color: $dark-grey;
                }
            }
        }
    }
}
