.--bwa-mobile-version {
    #--course-card {
        .flickity-viewport {
            .course-card.mobile-version {
                margin-right: 18px;
            }
        }

        .course-card {
            padding: 12px 12px 20px;
            width: 265px;
            height: auto !important;
            min-height: 312px;
            gap: 0 !important;
            .course-detail {
                margin-top: 16px;
                padding: 0 4px;

                .course_pricing,
                .disc-price,
                .origin-price {
                    font-size: 14px;
                    font-weight: 500;
                }

                .origin-price {
                    color: $black;
                }
                .course-name {
                    margin-bottom: 4px;
                    font-size: 16px;
                    min-height: 48px;
                    line-height: 24px;
                }
            }
            .thumbnail-course {
                width: 100%;
                border-radius: 16px;
                max-height: 150px;
            }
        }
        .thumbnailContainer,
        .course-card .course-thumbnail {
            width: 100% !important;
        }
        .swiper-slide {
            max-width: fit-content;
        }
        .course-footer {
            align-items: flex-end;
            padding: 0 4px;

            .star-rating {
                img {
                    width: 20px;
                    height: 20px;
                }
                span {
                    font-size: 14px;
                    font-weight: 400;
                    margin-top: 4px;
                }
            }
            .icon-level {
                width: 24px !important;
                height: 24px !important;
                object-fit: cover;
            }
        }
    }
}
