}

.text-sm {
    font-size: 14px !important;
}

.text-base {
    font-size: 16px !important;
}

.flr {
    float: right;
}

.fs-12 {
    font-size: 12px !important;
}

// Solving the grid and flexbox issue on Safari
.row::before,
.row::after,
.d-flex::before,
.d-flex::after {
    display: none !important;
}

.gy-30px {
    row-gap: 30px !important;
}

.gap-12px {
    gap: 12px !important;
}

.gap-14px {
    gap: 14px !important;
}

.mr-18 {
    margin-right: 18px !important;
}

.custom-scrollbar {
    overflow-x: auto;
    overflow-y: auto;

    &::-webkit-scrollbar {
        width: 7px;
        height: auto;
    }

    &::-webkit-scrollbar-thumb {
        background: $dark-blue;
        border-radius: 10px;
    }
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

.pointer-events-none {
    pointer-events: none !important;
}

.truncate-text {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.cursor-pointer {
    cursor: pointer !important;
}

.cursor-not-allowed {
    cursor: -moz-not-allowed;
    cursor: -webkit-not-allowed;
    cursor: not-allowed;
    user-select: none;
}

.rounded-16px {
    border-radius: 16px !important;
}

.container-pro {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;

    .content-pro {
        width: 296px;
        height: 288px;
        background: linear-gradient(
            180deg,
            rgba(255, 255, 255, 0) 0%,
            #ffffff 53.37%,
            rgba(255, 255, 255, 0) 100%
        );

        p {
            font-size: 24px;
            color: $black;
            font-weight: 700;
            height: 36px;
        }
    }
}

.tips-loader {
    background: $black-two;
    width: 100%;
    height: 100vh;
    z-index: 99;
    position: absolute;
    top: 0;
    left: 0;

    .content {
        padding-top: 300px;

        h3 {
            color: $white;
            font-weight: bold;
            font-size: 32px;
        }

        p {
            color: $soft-purple;
            font-size: 16px;
            font-weight: 200;
            line-height: 26px;
        }
    }
}

