.btn-danger {
    font-size: 16px;
    padding: 11px 42px 11px 42px;
    border-radius: 100px;
    border: 0;
    font-weight: 500;
    transition: all 0.4s;

    &:hover {
        -webkit-box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        -moz-box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        border: 0;
        transition: all 0.4s;
    }
}

.btn-off {
    font-size: 16px;
    padding: 6px 12px;
    border-radius: 4px;
    background-color: #dc3545;
    color: $white;
    border: 0;
    font-weight: 400;
    transition: all 0.4s;

    &:hover {
        -webkit-box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        -moz-box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        box-shadow: 10px 12px 20px 0px rgba(231, 235, 245, 1);
        border: 0;
        transition: all 0.4s;
        background-color: #bb2d3b;
        color: $white;
    }
}

.btn-primary {
    background: $blue;
    color: $white;
    font-size: 16px;
    padding: 11px 42px 11px 42px;
    border-radius: 100px;
    border: 0;
    font-weight: 600 !important;
    transition: all 0.4s;

    &:hover {
        -webkit-box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
        -moz-box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
        box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important;
        background: $blue;
        color: $white;
        border: 0;
        transition: all 0.4s;
    }

    @media screen and (max-width: 575px) {
