                height: 56px;
                object-fit: cover;
                border-radius: 8px;
            }
        }

        .badge {
            max-width: max-content;
        }
    }

    // mobile viewport
    .mobile-paginate-links {
        ul {
            column-gap: 22px !important;

            li.page-item {
                display: none;
                padding: 0 !important;

                &:first-child,
                &:nth-child(2),
                &:nth-last-child(2),
                &:last-child,
                // &.disabled,
                &:active {
                    display: block !important;
                }
            }
        }
    }
}

