.mastering-tools {
    @keyframes moveSlideshowToLeft {
        0% {
            transform: translateX(0%);
        }

        100% {
            transform: translateX(-100%);
        }
    }

    @keyframes moveSlideshowToRight {
        0% {
            transform: translateX(0%);
        }

        100% {
            transform: translateX(100%);
        }
    }

    .tools-slideshow {
        display: flex;
        flex-direction: row;
        row-gap: 30px;
        overflow: hidden;
        transform: translate3d(0, 0, 0);
        width: 100%;
        position: relative;
        height: auto;

        &:hover #toolsRow1,
        &:hover #toolsRow2 {
            animation-play-state: paused;
        }

        &.toRight {
            justify-content: flex-end;
        }

        #toolsRow1,
        #toolsRow2 {
            height: 100%;
            transform: translate3d(0, 0, 0);
            display: flex;
            animation: 25s linear infinite;
        }

        #toolsRow1 {
            animation-name: moveSlideshowToRight;
            .cardSlim {
                margin-right: 30px;
            }
        }

        #toolsRow2 {
            animation-name: moveSlideshowToLeft;
            .cardSlim {
                margin-left: 30px;
            }
        }

        .cardSlim {
            width: max-content;
            min-width: 288px;

            .subtitle {
                width: max-content;
            }
        }
    }
}
