#transactions_mobile {
    &.top-featured-section {
        .__most-up-to-date {
            row-gap: 16px;
            grid-template-columns: repeat(1, minmax(0, 1fr)) !important;

            .header {
                border-radius: 16px 16px 0 0;
            }
            .course- {
                &info-wrapper {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: flex-start;
                    height: 100%;

                    .price {
                        column-gap: 8px !important;
                    }

                    .badge {
                        padding: 0 7px;
                        height: 18px;
                        border-radius: 100px;
                        font-weight: 700;
                        font-size: 10px;
                        line-height: 15px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        &.bg- {
                            &danger {
                                background: $light-pink !important;
                                color: $soft-red;
                            }

                            &success {
                                background-color: $light-green !important;
                                color: $primary-green;
                            }

                            &warning {
                                background-color: $light-orange !important;
                                color: $orange;
                            }
                        }
                    }
                }
                &title {
                    display: block;
                    max-width: 100% !important;
                }
            }
        }
    }

    .transaction- {
        &label {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: $black;
        }
        &value {
            font-weight: 600;
            font-size: 14px;
            line-height: 21px;

            color: $black;
        }

        &info {
            padding: 6px 20px 12px 12px;
            border-radius: 0 0 16px 16px;
            row-gap: 8px;

            .text- {
                &danger {
                    color: $soft-red !important;
                }

                &success {
                    color: $primary-green !important;
                }

                &warning {
                    color: $orange !important;
                }
            }
        }
    }
}
