body .course-playing {

    transition: all .3s;

    a {
        color: #34364a;
    }

    .hamburger-menu {
        opacity: 0;
        visibility: hidden;
        height: 0;
        transition: all 0.2s;
    }

    .close-icon {
        margin-bottom: 30px;
    }

    .close-icon,
    .hamburger-menu {
        cursor: pointer !important;
        width: max-content;
    }

    .support-idn-creator {
        max-width: 800px;
        width: 100%;
        border-radius: 26px;
        background-color: #fff;
        color: #34364a;
        padding: 30px;
        margin-top: 50px;

        .title-creator h6 {
            font-weight: 700;
            font-size: 16px;
            text-transform: capitalize;
            margin-bottom: 8px;
        }

        p {
            font-weight: 300;
            font-size: 16px;
            margin-bottom: 0;
        }
    }

    main.main-wrapper {
        margin-left: 340px;
        margin-right: auto;
        position: relative;
        min-height: 100vh;
        /* This padding-top relative to .sidebar-aside margin-top */
        padding: 95px 0 30px;
        transition: all 0.3s;
        overflow: hidden;

        @media (max-width: 991px) {
            margin-left: 0 !important;
            padding: 30px 0 !important;

            .justify-content-center.row {
                padding-right: 16px;
                padding-left: 16px;
            }

            .justify-content-center.row .col-lg-11 {
                padding-left: 0;
                padding-right: 0;
            }

            .content-tab-mobile {
                padding-left: 16px;
                padding-right: 16px;
            }
        }
    }

    .not-found {
        width: 100%;

        @media (max-width: 991px) {
            margin: 0 auto;
            width: 100%;
            height: 100%;
            display: block;

            &.row {
                padding-right: 16px;
                padding-left: 16px;
            }
        }
    }

    @media (max-width: 991px) {
        .video-iframe {
            margin-top: 20px;
        }

        .mobile-video-player .heading {
            margin-bottom: 10px;
        }
    }

    .h4 {
        font-weight: 700;
        font-size: 32px;
    }

    #ratingModal,
    #ratingModalMobile {
        .modal-dialog {
            .modal-content {
                border-radius: 16px;
                overflow: hidden;

                .modal-body {
                    .form-group textarea {
                        border-radius: 8px;
                        border: 1px solid $dark-blue;
                    }

                    .rating {
                        display: inline-block;
                        position: relative;
                        height: 50px;
                        line-height: 50px;

                        label {
                            position: absolute;
                            top: 0;
                            left: 0;
                            height: 100%;
                            cursor: pointer;

                            &:last-child {
                                position: static;
                            }

                            &:nth-child(1) {
                                z-index: 5;
                            }

                            &:nth-child(2) {
                                z-index: 4;
                            }

                            &:nth-child(3) {
                                z-index: 3;
                            }

                            &:nth-child(4) {
                                z-index: 2;
                            }

                            &:nth-child(5) {
                                z-index: 1;
                            }

                            input {
                                position: absolute;
                                top: 0;
                                left: 0;
                                opacity: 0;
                            }

                            .icon {
                                float: left;
                                font-size: 30px !important;
                                color: transparent;
                            }

                            &:last-child .icon {
                                color: #e2e2e2;
                                margin-left: 4px;
                                margin-right: 4px;
                            }
                        }
                    }

                    .rating:not(:hover) label input:checked~.icon,
                    .rating:hover label:hover input~.icon {
                        color: rgb(244, 164, 43);
                        margin-left: 4px;
                        margin-right: 4px;
                    }

                    .rating label input:focus:not(:checked)~.icon:last-child {
                        color: #e2e2e2;
                        text-shadow: 0 0 5px rgb(244, 164, 43);
                        margin-left: 4px;
                        margin-right: 4px;
                    }
                }
            }
        }
    }

    @media (max-width: 992px) {
        .h4 {
            font-size: 24px;
        }
    }

}

body.dark .course-playing {
    transition: all 0.3s;
    background: #1c1f2c !important;

    .support-idn-creator {
        background-color: #252635;
        color: #f6f8fd;
    }

    #ratingModal,
    #ratingModalMobile {
        .modal-dialog {
            .modal-content {

                .modal-footer {
                    .btn-primary {
                        background-color: $blue !important;
                        color: #f6f8fd !important;
                    }
                }
            }
        }
    }

    .h4,
    .navigator span,
    .heading .h5,
    .heading .sub,
    p,
    h6,
    a,
    .sub,
    .tools-kelas,
    .nav-heading,
    h1.h2,
    .class-title,
    .prerequisite-course-name,
    .section-video-details {
        color: #f6f8fd !important;
    }
}

@import 'navbar';
@import 'sidebar';
@import 'tools';
@import 'mobile-tab-content';
@import 'buttons';
@import '../video-iframe';
@import 'quiz';
