body .course-playing {
    .course-navbar.navbar {
        background-color: #f6f8fd;
        transition: all 0.3s;
        font-weight: 500;

        .navbar-nav {
            .nav-link {
                font-size: 16px;
                color: #34364a !important;
                padding: 11px 14px;
                border-radius: 100px;
                transition: all 0.4s;

                &:hover {
                    color: #2447f9 !important;
                    transition: all 0.4s;
                }
            }
            .nav-item {
                .dropdown-menu {
                    right: 0;
                    left: 60px;

                    .dropdown-item {
                        color: #212529 !important;

                        &:active {
                            color: #e5e9e5 !important;
                        }
                    }
                }
            }
        }
    }
}

// Dark Mode
body.dark .course-playing {
    .course-navbar.navbar {
        background-color: #1c1f2c !important;

        .navbar-nav {
            .nav-link, .nav-item {
                font-size: 16px;
                color: #e5e9e5 !important;
                background-color: transparent !important;

                &:hover {
                    color: #fff !important;
                    transition: all 0.4s;
                }
            }
            .nav-item {
                .dropdown-menu {
                    .dropdown-item {
                        color: #212529 !important;

                        &:active {
                            color: #e5e9e5 !important;
                        }
                    }
                }
            }
        }
    }
}
