.__journey-mobile {
    .mobile_card {
        .icon {
            width: 60px;
            height: 60px;
            object-fit: cover;
        }

        h2.title {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: $black;
            margin-bottom: 0;
            max-width: 185px;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            word-wrap: break-word;
        }

        p {
            font-size: 14px;
            color: $dark-grey;
            margin-left: auto;
            margin-bottom: 0;
            min-width: max-content;
        }
    }

    &.detail-page {
        .header-section {
            margin-bottom: 36px;
        }
        .icon-roadmap {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            margin-right: 14px;
        }

        .title-roadmap {
            font-weight: 700;
            font-size: 24px;
            line-height: 36px;

            color: $black;

            &.line-clamp-3 {
                display: -webkit-box !important;
                -webkit-line-clamp: 3 !important;
                overflow: hidden !important;
            }
        }

        .roadmap- {
            &badge {
                font-weight: 500;
                font-size: 14px;
                line-height: 24px;
                width: 100px;
                height: 36px;
                margin-bottom: 22px;
            }
        }

        .__course-accordion {
            margin-bottom: 12px;
        }

        .accordion- {
            &body {
                padding-bottom: 0 !important;
            }
            &button {
                padding: 12px !important;
            }
            &item.section-video-list
                button.accordion-button:not(.collapsed)::after,
            &item.section-video-list button.accordion-button::after {
                background-image: url("themes/front/images/svgs/ic_arrow-vertical.svg");
                background-size: 11.5px 11.5px;
            }
        }

        .section_ {
            &title {
                font-weight: 700;
                font-size: 16px;
                line-height: 24px;

                color: $black;
            }

            &description {
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                /* identical to box height */

                color: $dark-grey;
            }
        }

        .course_item_container {
            margin: 0 !important;
            padding: 0 57px 14px 12px;
            gap: 16px !important;

            .course-thumbnail {
                height: 60px;
                max-height: 60px;
                width: 78px;
                object-fit: cover;
                border-radius: 14px;

                &-wrapper {
                    margin-right: 12px;
                }
            }

            .course-name {
                font-weight: 700;
                font-size: 14px;
                line-height: 21px;

                color: $black;
            }
        }
    }
}
