.mobile-auth-page {
    .content {
        padding-top: 50px !important;
        .form-group {
            padding: 0 16px !important;

            .auth- {
                &title {
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 30px;
        
                    color: $black;
                    margin-bottom: 20px;
                }

                &group {
                    &.email {
                        margin-bottom: 16px;
                    }
        
                    &.password {
                        margin-bottom: 10px;
                    }
                }
        
                &input {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 24px;
                    /* identical to box height, or 150% */
        
                    color: $black;

                    box-sizing: border-box;

                    outline: none;
                    border: none;

                    &::placeholder {
                        font-weight: 300;
                    }
        
                    &:focus {
                        border: 1px solid $primary-blue !important;
                        outline: none !important;
                    }
                }
        
                &link {
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 24px;
                    text-decoration-line: underline;
        
                    color: $primary-blue;
                    max-width: fit-content;
                }
        
                &masuk {
                    margin-top: 30px;
                }
        
                &divider {
                    &-wrapper {
                        margin: 20px 0;
                        max-height: fit-content;
                        .divider {
                            margin: 0 !important;
                            background-color: $base-grey !important;
                        }
                    }
                    &-text {
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 24px;
        
                        margin: 0 12px;
        
                        color: $light-grey;
                    }
                }
            }
        }
    }
    
}
