#notification-container {
    .header-third {
        font-weight: 700;
        font-size: 24px;
        line-height: 36px;
        color: #34364a;
    }

    #v-pills-tab {
        gap: 20px;
        button {
            width: 100% !important;
            max-width: 500px !important;
        }
        .active {
            background-color: white !important;
            border: none !important;
            .tab-title {
                font-weight: 700 !important;
            }
        }

        .tab-content {
            padding: 23px 16px;
            height: 100px;
        }

        .tab-img {
            width: 130px;
            height: 100%;
            object-fit: cover;
        }

        .tab-title {
            margin-block: auto;
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
            padding-right: 10px;
            color: #34364a !important;
        }

        .tab-badge {
            margin: auto;
            p {
                width: 48px;
                height: 30px;
                color: white;
                padding: 6px;
                background-color: #2447f9;
                font-weight: 600;
                font-size: 12px;
                line-height: 18px;
            }
        }
    }

    #v-pills-tabContent {
        // padding-left: 8px;
        .nav-item button {
            padding-inline: 20px !important;
        }
        .notif-cards {
            margin-bottom: 30px;
            gap: 16px;
            .notif-card {
                max-width: 600px;
                padding: 20px 20px 16px 20px;
                background-color: white;

                .notif-tag {
                    p {
                        border-radius: 100px;
                        width: fit-content;
                        height: fit-content;
                        padding: 8px 17px;
                        font-weight: 700;
                        font-size: 14px;
                        line-height: 21px;
                    }
                    .question::after {
                        content: "Question";
                        color: #2447f9;
                    }
                    .question {
                        background: #e8efff;
                    }
                    .new-reply::after {
                        content: "New Reply";
                        color: #22c58b;
                    }
                    .new-reply {
                        background: #dcfff3;
                    }
                }

                .notif-card-title {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 36px;
                }
                .notif-card-time {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 21px;
                    color: #999aa4;
                    margin-bottom: 0;
                }
            }
        }
    }
}
