.forum-discussion {
    color: $black;
    margin-top: 50px;
    margin-bottom: 100px;

    .search-form {
        margin-bottom: 50px;
        input.form-control.search-solution {
            background-color: $white;
            padding-left: 50px;
            padding-right: 50px;
            font-weight: 400;
        }

        @media screen and (max-width: 500px) {
            display: grid !important;
            width: 100%;

            input.form-control.search-solution {
                max-width: 100%;
            }
        }

        .ic_search {
            left: 12px;
        }

        .clear-field-x {
            right: 12px;
            z-index: 10;
        }

        .ic_search,
        .clear-field-x {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
        }
    }

    .section-forum {
        margin-top: 60px;
        width: 870px;
        max-width: 100%;
    }

    .section-header {
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .heading {
            font-size: 20px;
            font-weight: 700;
            margin-bottom: 6px;
        }

        p {
            margin-bottom: 0 !important;
        }
    }

    .user_profile {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 16px;

        img.avatar {
            width: 50px;
            height: 50px;
            object-fit: cover;
            border-radius: 999px;
        }

        .badge-blue {
            font-size: 8px;
            padding: 4px 9px;
        }

        .username {
            font-weight: 500;
            font-size: 16px;
        }

        .role {
            font-size: 14px;
            font-weight: 300;
            color: $dark-grey;
            margin-bottom: 0;
        }
    }

    .forum-card {
        padding: 30px;
        border-radius: 26px;
        background-color: $white;
        max-width: 870px;
        position: relative;

        // START: SUMMERNOTE STYLING
        .note-frame {
            border: 0 !important;
        }

        .note-editable {
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: 6px;
            transition: all 0.2s ease;
            &:focus {
                border: 1px solid #2447f9;
            }
        }

        .note-toolbar {
            position: absolute;
            bottom: 0;
            border-bottom: 0;
            border-top: 1px solid rgba(0, 0, 0, 0.2);
            z-index: 100;
            width: 100%;
        }

        .note-btn {
            border: 1px solid rgba(0, 0, 0, 0.2);
        }

        .note-btn-group {
            margin-right: 8px !important;
        }
        // END: SUMMERNOTE STYLING

        .pinned-badge {
            position: absolute;
            top: 0;
            right: 0;
            padding: 9px 30px;
            border-top-right-radius: 26px;
            border-bottom-left-radius: 26px;
            font-size: 14px;
            font-weight: 600;
            text-transform: uppercase;
            width: max-content;
        }

        .pinned-badge-dark {
            @extend .pinned-badge;
            background: $black;
            color: $white;
        }

        .pinned-badge-green {
            @extend .pinned-badge;
            background: $green;
            color: #0c4d36;
        }

        .detail {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            align-items: center;
            margin-bottom: 27px;

            .title {
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 6px;
            }

            .caption {
                font-size: 16px;
                color: $dark-grey;
            }
        }

        .replies {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding-right: 20px;

            @media screen and (max-width: 500px) {
                padding-right: 0;
            }

            div {
                color: $blue;
                font-size: 28px;
                font-weight: 600;

                @media screen and (max-width: 500px) {
                    font-size: 20px;
                }
            }

            p {
                font-size: 16px;
                color: $dark-grey;
                margin-bottom: 0;
            }
        }

        .paragraph {
            margin-top: 20px;
            margin-bottom: 0;
            max-width: 80%;

            @media screen and (max-width: 500px) {
                max-width: 100%;
            }
        }

        .replied_on {
            font-weight: 400;
            font-size: 14px;
            color: $dark-grey;
            margin: 8px 0 20px;
        }

        .dropgan{
            height: 34px;
            position: relative;
        }

        .dropdown.more-option-forum {
            z-index: 2;
            .dropdown-toggle {
                background: $dark-blue;
                border-radius: 999px;
                padding: 15px 8px;
                display: flex;
                column-gap: 3px;
                align-items: center;
                border: 0;

                &::after {
                    content: none;
                }

                span {
                    width: 3.5px;
                    height: 3.5px;
                    border-radius: 999px;
                    background-color: $black;
                }
            }

            .dropdown-menu {
                border-radius: 19px;
                border: 1px solid $dark-blue;
                padding: 0 !important;
                color: $black;
                overflow: hidden;
                min-width: auto !important;

                li {
                    .dropdown-item {
                        font-weight: 500;
                        font-size: 14px;
                        text-align: center;
                        padding: 11px 22px;
                        border-bottom: 1px solid $dark-blue;
                        transition: all ease 0.2s;
                    }

                    &:last-child {
                        .dropdown-item {
                            border-bottom: none;
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: 500px) {
        .section-forum .btn-primary {
            font-size: 14px;
            padding: 11px 24px;
        }
    }

    blockquote {
        font-style: italic;
        padding: 2px 10px;
        border-style: solid;
        border-color: $base-grey;
        border-width: 0;
        border-left-width: 8px;
    }

    ol,
    ul,
    dl {
        margin-right: 0px;
        padding: 0 40px;
    }

    .forum-card ul {
        list-style-type: disc;
    }

    .marker {
        background-color: yellow;
    }
}
