.__bwa-email {
    background-color: #030334;
    color: $black;

    p {
        margin: 0;
        font-size: 18px;
        line-height: 32px;
        @media screen and (max-width: 575px) {
            font-size: 14px;
            line-height: 26px;
        }
    }

    .email-container {
        background-color: $soft-blue;
        padding: 30px 0;
        margin: 50px 0;
        max-width: 630px;

        @media screen and (max-width: 575px) {
            margin: 0;
            padding-bottom: 60px;
        }
    }

    main > header {
        padding: 0 30px;
        .text {
            h2 {
                font-size: 18px;
                font-weight: 700;
            }
            p {
                font-size: 12px;
                margin-top: 4px;
            }
        }
    }

    article {
        padding: 0 30px;
        header {
            p {
                color: $primary-green;
                font-weight: 500;
                margin-bottom: 10px;
            }

            h1 {
                font-weight: 700;
                font-size: 32px;
                line-height: 48px;
                max-width: 400px;
                @media screen and (max-width: 575px) {
                    font-size: 24px;
                    line-height: 36px;
                }
            }
        }

        .main-paragraph {
            margin-bottom: 30px;
            margin-top: 8px;
            font-weight: 400;
        }
    }

    .email-cover {
        width: 100%;
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }
}
