#tutorialPreview {
    margin-bottom: 50px;
    color: $black;

    .video-container {
        background-color: #01010e;
        .video-iframe {
            border-radius: 0;
            margin: 0 auto;
        }
    }

    div.container {
        header,
        section {
            max-width: 700px;
        }
    }

    .main-content {
        margin-top: 50px;
        @media screen and (max-width: 575px) {
            margin-top: 20px;
        }
    }

    header {
        h1.header-primary {
            margin-bottom: 10px;

            @media screen and (max-width: 575px) {
                font-size: 24px;
                line-height: 38px;
            }
        }
    }

    section {
        margin-top: 40px;
    }

    .about {
        p {
            line-height: 34px;
            font-size: 16px;
            margin-bottom: 16px;

            &:last-child {
                margin: 0;
            }
        }
    }

    .course-card-responsive {
        height: auto;
    }
}
