body .course-playing {

    .item-tool {
        box-sizing: border-box;
        padding: 30px;
        background: $white;
        border-radius: 14px !important;
        transition: all .3s;
    }

    .item-software {
        border-radius: 12px;
        border: 0;
        background-color: #fff;
        padding: 30px 30px 20px;
    }

    div.tools-kelas {
        .tools-heading {
            margin-top: 50px;
            font-weight: 700;
            font-size: 20px;
        }

        p {
            font-weight: 400;
            font-size: 16px;
        }
    }

}

body.dark .course-playing {
    .item-tool {
        background: #252635;

    }

    .item-software {
        background-color: #252635;
        color: #F6F8FD;
        max-width: 100%;
    }

}
