.card-review {
    padding: 20px;
    background-color: $white;
    border-radius: 16px;

    .user-avatar {
        object-fit: cover;
        max-width: 70px;
        width: 70px;
        height: 70px;
        max-height: 70px;
        border-radius: 999px;
    }

    .username {
        color: $black;
        margin-bottom: 0;
        font-size: 16px;
        font-weight: 600;
    }

    .role {
        font-weight: 300;
        font-size: 14px;
        color: $dark-grey;
        margin-top: 4px;
        margin-bottom: 0;
    }

    .heading {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 10px;
    }

    p.review {
        font-weight: 400;
        font-size: 16px;
        line-height: 28px;
        word-break: break-word;
        overflow: hidden;
        position: relative;
    }

    .readMoreReview {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 3;
        color: $blue;
        background: linear-gradient(
            270deg,
            #ffffff 80%,
            rgba(255, 255, 255, 0.6) 99.4%
        );
        padding-left: 44px;
        font-size: inherit;
    }

    .rating {
        display: flex;
        align-items: center;
        gap: 4px;
        img {
            width: 20px !important;
            height: 20px;
        }
    }
}
