.ground,
.ground-login {
    position: fixed;
    z-index: 2147483002;
    display: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);

    p {
        margin-bottom: 0 !important;
    }

    .box-master {
        top: 50%;
        transform: translateY(-50%);
        border: 0;
        outline: 0;
        border-radius: 64px;
        background: $white;
        position: relative;
        box-sizing: border-box;
        color: $black;
        margin: auto;
        display: flex;
        flex-direction: column;

        .ic-close {
            position: absolute;
            z-index: 100;
            top: -12px;
            right: -2px;
            width: 48px;
            cursor: pointer;
        }

        .ic-showcase {
            width: 100px;
        }

        .description {
            .big-text {
                font-size: 20px;
                font-weight: 700;
                line-height: 30px;
                text-align: center;
                margin-bottom: 10px;
            }

            p {
                font-size: 16px;
                font-weight: 300;
                text-align: center;

                &.sm {
                    font-size: 12px !important;
                }

                span {
                    font-weight: 700 !important;
                }
            }
        }

        .d-grid {
            gap: 12px;

            a {
                text-align: center;
                padding: 11px 72px;
            }
        }

        .copied-alert {
            padding: 12px 32px;
            background: $white;
            color: $black;
            font-weight: 400;
            font-size: 16px;
            text-align: center;
            border-radius: 16px;
            position: absolute;
            z-index: 999;
            bottom: -9440px;
            // display: none;
        }
    }

    .box {
        @extend .box-master;
        align-items: center;
        gap: 30px;
        max-width: 440px;
        padding: 50px 60px;

        @media (max-width: 575px) {
            top: 25%;
            transform: translateY(-25%);
            padding: 30px;
            border-radius: 34px;
        }
    }

    .box.box-welcome-boarding {
        max-width: 650px !important;

        @media screen and (max-width: 992px) {
            max-height: 95vh;
            overflow-y: auto;
        }

        @media screen and (max-width: 575px) {
            padding: 30px !important;
        }

        .description {
            p {
                max-width: 443px;
            }
        }
    }

    .box-870 {
        @extend .box-master;
        max-width: 870px;

        @media (max-width: 575px) {
            max-width: 354px;
            border-radius: 32px;
        }

        .showcase .banner-login {
            height: 100%;
            width: 100%;
            border-radius: 64px 0 0 64px;
            object-fit: cover;
        }

        .modal-top-banner {
            width: 100%;
            background-color: $black;
            color: $white;
            font-weight: 500;
            font-size: 16px;
            border-radius: 60px 60px 0 0;

            p {
                padding: 23px 50px;
            }

            @media (max-width: 575px) {
                border-radius: 32px 32px 0 0;
                p {
                    padding: 24px 30px;
                }
            }
        }

        .content {
            column-gap: 40px;
            row-gap: 24px;
            padding: 50px;

            @media (max-width: 575px) {
                padding: 30px 30px 50px;
            }

            .img-coming-course {
                object-fit: cover;
                border-radius: 30px;
                width: 50%;
                height: 250px;

                @media (max-width: 991px) {
                    width: 100%;
                }

                @media (max-width: 575px) {
                    border-radius: 21.6px;
                    max-width: 100%;
                    height: 180px;
                }
            }

            .course-name {
                font-size: 24px;
                font-weight: 700;

                @media (max-width: 575px) {
                    font-size: 20px;
                }
            }

            .course-price {
                font-size: 16px;
                font-weight: 400;
                margin: 12px 0 50px !important;
                line-height: 28px;
                color: $dark-grey;

                .disc-price {
                    font-weight: 400 !important;
                    margin-right: 12px;
                }
            }

            .btn-primary {
                max-width: 210px;

                @media (max-width: 575px) {
                    max-width: 100%;
                }
            }
        }

        .form-control:focus {
            box-shadow: none;
        }
    }
}
