.card-container {
    position: relative;
    padding: 30px;
    border-radius: 16px;
    border: none;
    background: $white;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    row-gap: 24px;
    color: $black;
    height: 100%;

    a {
        border-bottom: none;
    }
}

.labelJoinedCourse {
    padding: 8px 12px;
    background-color: $ocean-blue;
    border-radius: 50px;
    display: flex;
    align-items: center;
    gap: 6px;
    max-width: max-content;
    margin-top: auto;

    img {
        width: 24px;
        height: 24px;
    }

    p.__text {
        margin: 0;
        font-size: 14px;
        font-weight: 600;
        color: #fff;
        text-transform: uppercase;
    }

    @media screen and (max-width: 575px) {
        padding: 4px 8px;
        gap: 4px;
        margin: 4px 12px 12px auto;

        p.__text {
            font-size: 12px;
        }

        img {
            width: 18px;
            height: 18px;
        }
    }
}

.thumbnailContainer {
    width: 110px;
    height: 100%;

    ._btn-play {
        position: absolute;
        bottom: 50%;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, 50%);
        transition: all 0.4s ease-in-out;
        width: 32px;

        &:hover {
            filter: drop-shadow(0px 5px 12px rgba(61, 88, 255, 0.5));
        }
    }

    &:focus-visible,
    &:focus,
    &:focus-within {
        outline: none !important;
        border: none !important;
    }

    @media screen and (min-width: 768px) {
        width: 100%;
    }

    @media screen and (min-width: 1000px) {
        overflow: hidden;
        transition: all 0.4s ease-in-out;

        ._btn-play {
            opacity: 0;
            width: 40px;
            bottom: -50%;
        }

        &:hover {
            ._btn-play {
                opacity: 1;
                bottom: 50%;
            }
        }
    }
}

.course-card {
    @extend .card-container;

    .thumbnailContainer {
        width: 100%;
        height: auto;
    }

    .thumbnail-course,
    .thumbnail-bootcamp {
        border-radius: 16px;
        object-fit: cover;
        width: 100%;
        max-height: 210px;
    }

    @media screen and (min-width: 768px) {
        .thumbnail-course,
        .thumbnail-bootcamp {
            min-height: 200px !important;
        }
    }

    @media (min-width: 600px) {
        .thumbnail-bootcamp {
            min-height: 260px;
        }
    }

    .course-detail {
        .course-name {
            color: $black;
            z-index: 80 !important;
            position: relative;
            font-weight: 700 !important;
            font-size: 20px;
            min-height: 60px;
            line-height: 30px;
            @extend .line-clamp;

            @media screen and (min-width: 576px) and (max-width: 991px) {
                height: 65px;
            }
        }
    }

    .course-footer {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        gap: 4px;

        .star-rating {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 2px;

            img {
                width: 24px;
            }

            span {
                font-weight: 500;
                font-size: 16px;
                margin-left: 2px;
            }
        }

        .icon-level {
            position: relative;
            z-index: 555;
            cursor: pointer;
        }

        @media only screen and (min-width: 768px) and (max-width: 1250px) {
            .star-rating {
                img {
                    width: 20px !important;
                }

                span {
                    font-size: 14px !important;
                }
            }

            .icon-level {
                width: 24px !important;
            }
        }
    }
}

.course-card-responsive {
    @extend .card-container;

    &:not(.mobile-version) {
        @media screen and (max-width: 767px) {
            padding: 0;
            row-gap: 16px;
            border-radius: 14px;
            min-height: auto;

            .thumbnail-course {
                border-top-left-radius: 16px;
                border-bottom-left-radius: 16px;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                width: 110px;
                height: 100%;
                min-height: 110px !important;
            }
        }
    }

    padding: 0 !important;
    border-radius: 16px;
    row-gap: 0;
    height: 100%;
    min-height: 380px;

    .thumbnail-course {
        min-height: 190px;
        max-height: 190px;
        width: 100%;
        border-radius: 0;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
        object-fit: cover;
    }

    .labelJoinedCourse {
        margin-left: 16px;
        margin-bottom: 16px;
    }

    .course-detail,
    // .labelJoinedCourse,
    .course-footer {
        padding-right: 16px;
        padding-left: 16px;
    }

    .course-detail {
        padding-top: 16px;
        margin-bottom: 20px;

        @media screen and (max-width: 767px) {
            padding: 14px;
        }

        .disc-price {
            margin-right: 6px;
        }

        .origin-price {
            color: $dark-grey;
        }

        .course-name {
            color: $black;
            z-index: 80 !important;
            position: relative;
            font-weight: 700 !important;
            font-size: 18px !important;
            line-height: 27px;
            min-height: 55px;
            @extend .line-clamp;

            &:not(.mobile-version) {
                @media screen and (max-width: 767px) {
                    font-size: 16px;
                    line-height: 26px;
                    min-height: 50px;
                }
            }
        }

        .course-price-wrapper {
            font-size: 16px;

            &:not(.mobile-version) {
                @media (max-width: 767px) {
                    font-size: 14px;
                    color: $dark-grey;
                    font-weight: 300;
                }
            }
        }
    }

    .course-footer {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        gap: 4px;
        padding-bottom: 16px;

        &:not(.mobile-version) {
            @media screen and (max-width: 767px) {
                padding: 16px;
                padding-top: 0;
                align-items: center;
            }
        }

        .star-rating {
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;
            gap: 4px;

            img {
                width: 20px;

                &:not(.mobile-version) {
                    @media screen and (max-width: 767px) {
                        width: 18px;
                        gap: 4px;
                    }
                }
            }

            span {
                font-size: 14px;
                font-weight: 500;

                &:not(.mobile-version) {
                    @media screen and (max-width: 767px) {
                        font-size: 14px;
                        line-height: 20px;
                        margin-top: 0;
                    }
                }
            }
        }

        .author_avatar {
            width: 32px;
            height: 32px;
            border-radius: 100px;
        }

        .icon-level {
            position: relative;
            z-index: 555;
            cursor: pointer;
        }

        &:not(.mobile-version) {
            @media only screen and (min-width: 768px) and (max-width: 1250px) {
                .star-rating {
                    img {
                        width: 20px !important;
                    }

                    span {
                        font-size: 14px !important;
                    }
                }

                .icon-level {
                    width: 24px !important;
                }
            }
        }
    }
}

.course-card.mini {
    padding: 0 !important;
    border-radius: 16px;
    row-gap: 0;

    .thumbnail-course {
        min-height: 190px;
        max-height: 190px;
        width: 100%;
        border-radius: 0;
        border-top-left-radius: 16px !important;
        border-top-right-radius: 16px !important;
        object-fit: cover;
    }

    .labelJoinedCourse {
        margin-left: 16px;
        margin-bottom: 16px;
    }

    .course-detail,
    // .labelJoinedCourse,
    .course-footer {
        padding-right: 16px;
        padding-left: 16px;
    }

    .course-detail {
        padding-top: 16px;
        margin-bottom: 20px;

        .course-name {
            font-size: 18px !important;
            line-height: 27px;
            min-height: 55px;
        }

        .origin-price {
            color: $dark-grey;
        }
    }

    // .labelJoinedCourse,
    .course-footer {
        padding-bottom: 16px;
    }

    .course-footer {
        align-items: center;
    }

    .star-rating {
        img {
            width: 20px;
        }

        span {
            font-size: 14px;
            font-weight: 500;
            margin-top: 4px;
        }
    }
}

@keyframes skeleton-loader {
    0% {
        background-color: hsl(200, 20%, 85%);
    }

    100% {
        background-color: hsl(200, 20%, 95%);
    }
}

.skeleton-animation {
    opacity: 0.7;
    animation: skeleton-loader 0.6s linear infinite alternate;
    border-radius: 0.25rem;
}

.skeleton-card {
    display: flex;
    flex-direction: column;
    border-radius: 16px;
    background: $white;
    gap: 24px;

    .img-skeleton {
        @extend .skeleton-animation;
        background-color: $dark-grey;
        width: 100%;
        height: 198px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }

    .detail {
        padding: 0 16px;

        .title-skeleton {
            @extend .skeleton-animation;
            background-color: $dark-grey;
            height: 37px;
            width: 100%;
            margin-bottom: 8px;
        }

        .type-skeleton {
            @extend .skeleton-animation;
            background-color: $dark-grey;
            height: 24px;
            width: 35%;
        }
    }

    .skeleton-footer {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 16px 16px;

        .stars-skeleton {
            @extend .skeleton-animation;
            background-color: $dark-grey;
            width: 40%;
            height: 24px;
        }

        .icon-skeleton {
            @extend .skeleton-animation;
            background-color: $dark-grey;
            border-radius: 100px !important;
            width: 32px;
            height: 32px;
        }
    }
}

.skeleton-card-mobile {
    display: flex;
    flex-direction: column;
    gap: 14px;
    border-radius: 16px;
    background-color: $white;
    overflow: hidden;

    .content {
        display: flex;
        align-items: center;
        gap: 14px;

        .thumbnail {
            @extend .skeleton-animation;
            height: 110px;
            width: 110px;
        }

        .info {
            padding: 14px 8px 0;

            .title-text {
                @extend .skeleton-animation;
                width: 230px;
                height: 22px;
            }

            .price-tag {
                @extend .skeleton-animation;
                margin-top: 30px;
                width: 80px;
                height: 16px;
            }
        }
    }

    .footer {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 16px 20px;

        .left {
            @extend .skeleton-animation;
            width: 172px;
            height: 22px;
        }

        .right {
            @extend .skeleton-animation;
            width: 32px;
            height: 32px;
            border-radius: 100px;
        }
    }
}

.tippy-box[data-theme~="bwa_patungan"],
.tippy-tooltip.bwa_patungan-theme {
    .tippy-backdrop {
        background-color: $white;
    }

    background-color: $white;
    border-radius: 8px;
    padding: 15px 11px !important;
    box-shadow: 0px 0px 20px 4px rgb(231 235 245 / 40%);
    -webkit-box-shadow: 0px 0px 20px 4px rgb(231 235 245 / 40%);
    -moz-box-shadow: 0px 0px 20px 4px rgb(231 235 245 / 40%);

    .icon-info-patungan {
        width: 32px;
    }

    .title {
        color: $green !important;
        font-size: 16px !important;
        font-weight: 700 !important;
        margin: 0 0 10px;
        text-align: left;
    }

    .sub-title {
        color: $dark-grey;
        font-size: 14px;
        font-weight: 400;
        margin-bottom: 0;
        text-align: left;
    }
}

@media screen and (max-width: 991px) {
    .course-card .thumbnail-course,
    .course-card-responsive .thumbnail-course {
        min-height: auto !important;
    }

    .main-carousel-recommended .course-card .thumbnail-course,
    .main-carousel-recommended .course-card-responsive .thumbnail-course {
        min-height: 200px !important;
    }
}

#modalTrailerCourse {
    .modal-header {
        background-color: $soft-blue;
        border-radius: 16px 16px 0 0;
        border: 0;
    }

    .modal-body {
        background-color: $soft-blue;
        border-radius: 0 0 16px 16px;
        border: none;
        overflow: hidden;
        .video-iframe,
        iframe {
            border: none;
            border-radius: 0 0 16px 16px !important;
        }
        .embed-responsive {
            height: 100%;
        }
    }
}
