#static_page {
    .page- {
        &title {
            font-weight: 700;
            font-size: 30px;
            line-height: 42px;
        }
        &sub-title {
            font-weight: 600;
            font-size: 18px;
        }
        &description {
            font-size: 14px;
            font-weight: 400;
        }
        &icon {
            font-size: 72px;
        }
    }

    .table {
        font-size: 12px !important;
        table-layout: fixed;
    }

    a,
    ol,
    ul {
        font-size: 14px;
    }

    ul {
        list-style-type: disc;

        &.circle {
            list-style-type: circle;
        }
    }

    ol,
    ul {
        padding: 0;
        margin-left: 30px;
    }

    .line-height-tall {
        line-height: 28px;
    }

    .sample-course-card {
        border-radius: 5px;
        border: 1px solid $light-grey;
        overflow: hidden;
        height: 120px;

        .course- {
            &title {
                font-size: 12px;
                line-height: 14px;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                color: $black;

                word-wrap: break-word;
                display: -webkit-box;
                height: auto;
                max-width: 100%;
            }
            &description {
                font-size: 10px;
                line-height: 12px;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                color: $dark-grey;

                word-wrap: break-word;
                display: -webkit-box;
                height: auto;
                max-width: 100%;
                margin: 8px 0;
            }

            &url {
                font-size: 10px;
                line-height: 12px;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                color: $black;

                word-wrap: break-word;
                display: -webkit-box;
                height: auto;
                max-width: 100%;
            }

            &thumbnail {
                width: 250px !important;
                object-fit: cover;
                // object-position: center;
                object-position: 50% 50%;
            }
        }
    }
}
