body .course-playing {

    .btn.disabled,
    a.disabled,
    .btn:disabled,
    fieldset:disabled .btn {
        pointer-events: all !important;
    }

    .btn-master {
        padding: 11px 26px;
        transition: all 0.3s;
        border-radius: 100px;
        font-size: 16px;
        font-weight: 500;
        width: max-content;
        border: 0;

        &:hover {
            box-shadow: none;
        }

        // @media (max-width: 576px) {
        //     width: auto !important;
        // }
    }

    .btn-success {
        background-color: #28a745;
        color: #fff;
    }

    .btn-succes,
    .btn-danger {
        &:hover {
            box-shadow: none;
        }
    }

    .btn-primary {
        @extend .btn-master;
        color: #F6F8FD;
    }

    .btn-review,
    .btn-secondary {
        @extend .btn-master;
        padding: 11px 16px !important;
        background: #e5e9f2 !important;
        color: #34364a !important;
    }

    .btn-selesai {
        @extend .btn-master;
        background-color: #1abc9c !important;
        color: #F6F8FD;
    }

    .btn-next-quiz {
        padding: 14px;
        background: $black;
        margin: 20px;
        border-radius: 8px;
        color: #fff;
        transition: all 0.3s;
    }

    .btn-repeat-quiz {
        padding: 14px;
        background: $dark-blue;
        margin: 20px;
        font-weight: 500;
        border-radius: 8px;
        color: $black;
        transition: all 0.3s;
        cursor: pointer;
        text-decoration: none;
    }
}

body.dark .course-playing {

    .btn-secondary {
        color: $black !important;
    }

    .btn-selesai {
        color: #34364a;

        &:hover {
            background-color: #16aa8c !important;
        }
    }

    .btn-next-quiz,
    .btn-repeat-quiz {
        background: #252635;
    }
}
