.sidebar-nd {
    border-radius: 26px;
    background: $white;
    margin-left: 25px;
    border-radius: 26px;
    background: $white;
    width: 290px;
    height: 78vh;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    transition: 0.3s ease-in-out;

    .content {
        padding: 30px;
        overflow-y: auto;
        height: 100%;
        @extend .webkit-scrollbar-thumb;
    }

    .menus {
        .item.active {
            .icon {
                filter: brightness(0) saturate(100%) invert(26%) sepia(87%)
                    saturate(1682%) hue-rotate(221deg) brightness(105%)
                    contrast(111%);
            }
            p {
                a {
                    color: $black;
                }
            }
        }

        .item {
            margin-bottom: 36px;

            .icon {
                float: left;
                margin-right: 12px;
            }

            p {
                padding-top: 3px;

                a {
                    font-size: 16px;
                    text-decoration: none;
                    font-weight: 500;
                    color: $dark-grey;

                    &:hover {
                        color: $black;
                    }
                }
            }
        }
    }

    .user {
        .avatar {
            width: 70px;
            height: 70px;
            border-radius: 100px;
            object-fit: cover;
        }

        .info {
            h3 {
                color: $black;
                font-weight: bold;
                font-size: 18px;
                margin-bottom: 0;
            }

            p {
                font-size: 14px;
                font-weight: 400;
                color: $dark-grey;
            }
        }
    }
}
