.stack-card-group {

    .box-shadow-stack {
        box-shadow: 0px 9px 30px rgba(234, 236, 240, 0.5);
    }

    .stack-card-ornament {
        transition: all .3s;
        @extend .box-shadow-stack;
    }

    .stack-card-ornament {
        border-radius: 14px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        height: 100%;
        background: $white;

        &.sco-1 {
            z-index: -1;
            bottom: -7px;
            width: 88%;
        }

        &.sco-2 {
            z-index: -2;
            bottom: -15px;
            width: 78%;
        }
    }

    &:hover {

        .stack-card-ornament {
            box-shadow: 0px 9px 30px rgba(234, 236, 240, 0.4);
        }

        .stack-card-ornament {
            &.sco-1 {
                bottom: -15px;
            }

            &.sco-2 {
                bottom: -30px;
            }
        }
    }
}
