#sale_mobile {
    .item-flash {
        margin-top: 16px;

        &:last-child {
            margin-bottom: 50px;
        }
    }

    .item-flash {
        .course-card-responsive {
            row-gap: 0px !important;

            .course-price-wrapper,
            .disc-price,
            .origin-price {
                font-weight: 500;
                font-size: 12px;
                line-height: 18px;
            }

            .disc-price {
                margin-right: 5px;
            }

            .course- {
                &card-content {
                    padding-left: 12px;
                    padding-top: 12px;
                    padding-bottom: 10px;

                    column-gap: 12px;
                }
                &detail {
                    padding: 0;
                    padding-bottom: 0px;
                    margin-bottom: 0;
                }
                &name {
                    font-size: 14px !important;
                    line-height: 21px;

                    min-height: 0 !important;
                    max-width: 80%;
                }

                &price-wrapper {
                    margin-top: 2px !important;
                    gap: 0 !important;
                }

                &footer {
                    padding: 12px;

                    .star-rating {
                        span {
                            font-weight: 500;
                            font-size: 12px !important;
                            line-height: 18px !important;
                            margin-top: 1px;
                        }

                        img {
                            gap: 4px;
                            width: 14.5px;
                            height: 14.5px;
                        }
                    }

                    .author_avatar {
                        width: 24px;
                        height: 24px;
                    }

                    .mentor-wrapper {
                        column-gap: 4px;
                    }
                }
            }

            &.card-type-transactions {
                min-height: fit-content;
                .course- {
                    &card-content {
                        padding-right: 12px;
                        padding-bottom: 12px;
                    }
                    &name-custom {
                        font-weight: 700;
                        font-size: 14px;
                        line-height: 21px;

                        color: $black;

                        max-width: auto;

                        display: -webkit-box !important;
                        -webkit-line-clamp: auto !important;
                        overflow: visible !important;

                        min-height: 42px;
                    }
                    &detail {
                        min-height: 60px;

                        .badge {
                            margin-left: 6px;
                        }
                    }

                    &price-wrapper {
                        margin-top: 2px;
                    }
                }

                &.transaction-detail {
                    border-radius: 16px 16px 0 0;
                }
            }
        }

        .thumbnailContainer,
        .thumbnail-course {
            height: 60px;
            width: 78px;
            object-fit: cover;
            margin-top: 0;
            border-radius: 14px;
            min-height: auto !important;
        }

        ._btn-play {
            width: 20px;
            height: 20px;
        }
    }
}
