.__course-accordion {
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    transition: all 0.3s;

    .rounded-number {
        width: 36px;
        height: 36px;
        padding: 5px 0;
    }

    .course_details {
        display: flex;
        align-items: center;
        gap: 12px;

        .section_title {
            margin-bottom: 2px;
            font-weight: 700;
        }
    }

    .accordion-item.section-video-list {
        border: 0 !important;
        background-color: $white;
        padding: 0 !important;
        transition: all 0.3s;
        border-radius: 16px;

        button.accordion-button {
            border-radius: 16px;
            transition: all 0.3s !important;
            padding: 12px;
            background-color: transparent;

            .section-heading-video {
                margin-bottom: 0 !important;
            }

            &:focus,
            &:not(.collapsed) {
                color: initial;
                background-color: initial;
                box-shadow: none;
                transition: all 0.3s !important;
                border: none;
            }

            &:not(.collapsed)::after,
            &::after {
                background-image: none;
            }

            &::after {
                background-position: center;
                width: 24px;
                height: 24px;
                border-radius: 100px;
                background-color: $dark-blue;
                background-size: 14px;
            }
        }

        .accordion-body {
            padding: 0 0 14px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            transition: all 0.3s;
        }

        ul.course_item_container {
            width: 242px;
            margin: 0 auto;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            gap: 10px;

            .item-video {
                margin-bottom: 0;
                display: flex;
                align-items: center;
                gap: 12px;
                position: relative;
            }
        }
    }
}

.__course-accordion
    .accordion-item.section-video-list
    button.accordion-button:not(.collapsed)::after,
.__course-accordion
    .accordion-item.section-video-list
    button.accordion-button::after {
    background-image: url("/themes/front/images/svgs/ic_arrow-vertical.svg");
}
