.card-salary {
    padding: 16px;
    border-radius: 16px;
    background-color: $white;
    display: flex;
    align-items: center;
    gap: 16px;
    color: $black;

    > img {
        width: 50px;
        height: 50px;
    }

    .ic-check {
        width: 18px;
        height: 18px;
    }

    h4.average-salary {
        font-weight: 700;
        font-size: 26px;
        line-height: 39px;

        span {
            color: $blue;
        }

        small {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
        }
    }

    .job-role {
        gap: 6px;

        p {
            line-height: 21px !important;
        }
    }

    p.thin-text {
        font-size: 10px;
        color: $dark-grey;
        font-style: italic;
        margin: 0;
        line-height: 15px;
    }
}
