.blur-background {
    background-color: var(
        --header-background,
        hsla(0, 0%, 100%, 0.8)
    ) !important;
    backdrop-filter: blur(10px) saturate(150%);
    -webkit-backdrop-filter: blur(10px) saturate(150%);
    transition: background-color 0.3s linear;
}

.navbar-nd {
    padding-top: 20px;
    padding-bottom: 20px;

    p {
        margin: 0;
    }

    .container-fluid {
        padding-right: 50px;
        padding-left: 50px;
    }
    .user-photo {
        width: 50px;
        height: 50px;
        border-radius: 100px;
        object-fit: cover;
    }
    .user-name {
        margin-right: 14px;
        font-size: 16px;
        font-weight: 500;
        color: $black;
        width: max-content !important;
        white-space: nowrap;
    }
    .btn-daftar,
    .btn-masuk {
        @media screen and (max-width: 1200px) {
            min-width: 100%;
        }
    }

    .navbar-brand {
        margin-right: 50px;
    }

    .right-menu {
        display: flex;
        align-items: center;
        gap: 20px;

        @media screen and (max-width: 991px) {
            align-items: flex-start;
        }
    }

    .dropdown-menu {
        margin-bottom: 0 !important;
        overflow: hidden;
    }

    .dropdown-menu.__2022 {
        background-color: $white;
        padding: 30px;
        border-radius: 16px;
        border: none;
        margin-top: 33px;
        margin-right: 0;
        margin-bottom: 0;
        width: max-content;

        @media screen and (max-width: 500px) {
            padding: 20px;
            margin-top: 16px;
            width: 95%;
        }

        &.show {
            display: flex;
            flex-direction: column;
            row-gap: 30px;
        }

        .dropdown-item {
            position: relative;
            display: flex;
            gap: 14px;
            align-items: center;
            transition: all 0.3s ease-in-out;
            min-width: max-content;
            width: max-content;
            white-space: normal !important;
            padding: 0 !important;

            &:focus,
            &:hover {
                background-color: transparent !important;

                .title {
                    color: $blue;
                }

                .subtitle {
                    color: $black;
                }
            }

            img {
                width: 48px;
            }

            .title {
                font-size: 16px;
                color: $black;
                font-weight: 500;
                text-transform: capitalize;
            }

            .subtitle {
                margin-top: 2px;
                margin-bottom: 0;
                font-size: 13px;
                color: $dark-grey;
                font-weight: 300;
            }
        }
    }

    .nav-item {
        margin-right: 0px !important;

        .nav-link {
            width: max-content;
            font-size: 16px !important;
            padding: 0 !important;
        }
    }

    .basic-menus {
        column-gap: 50px;
        row-gap: 8px;
        a {
            color: $black !important;
            font-size: 16px;
            font-weight: 500;
            transition: all 0.4s;
            &:focus {
                color: $blue !important;
                transition: all 0.4s;
            }
            &:hover {
                color: $blue !important;
                transition: all 0.4s;
            }
        }
        .nav-item {
            display: flex;
            flex-direction: column;
            @media screen and (min-width: 1200px) {
                flex-direction: row;
                justify-content: center;
                align-items: center;
            }
            .active {
                color: $blue !important;
            }
        }
    }

    .nav-item .dropdown-toggle {
        transition: none;
        &:focus {
            font-weight: 600 !important;
            color: $black !important;
        }

        &::after {
            vertical-align: 0.2em;
        }
    }
    // START: Menu 'Kelas' styling
    .dropdown-menu[aria-labelledby="listKelasDropdown"],
    .dropdown-menu[aria-labelledby="listAlurBelajarDropdown"] {
        padding: 0 !important;
        .dd-item-list {
            column-gap: 58px;
            padding: 50px 38px 50px 0;
            margin: 0 !important;

            .col {
                display: flex;
                flex-direction: column;
                row-gap: 20px;
                width: max-content;
            }

            a {
                min-width: max-content;
                width: max-content;
            }
        }

        .sidecontent {
            text-align: center;
            background-color: $ocean-blue;
            padding: 53px 33px 52px 34px !important;
            min-width: 300px;
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            h5 {
                color: $white;
                font-weight: 600;
                font-size: 24px;
                line-height: 36px;
                margin: 6px 0;
            }
            p {
                margin: 0;
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                color: #a9a4bc;
                &:first-of-type {
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 18px;
                    color: $primary-green;
                }
            }
            img {
                max-width: 294px;
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 0;
            }

            a.btn-primary {
                margin-top: 50px;
                color: $white !important;
                position: relative;
                z-index: 10;
            }
        }

        &.show {
            flex-direction: row !important;
            gap: 38px;
            align-items: center;
        }
    }

    .dropdown-menu[aria-labelledby="listKelasDropdown"] {
        left: -229px !important;
    }
    // END: Menu 'Kelas' styling

    .dropdown-menu[aria-labelledby="listAlurBelajarDropdown"] {
        left: -340px !important;
        .sidecontent {
            padding: 63px 44px 62px 46px !important;
        }
        .dd-item-list {
            display: flex;
            flex-direction: column;
            row-gap: 24px;
        }
    }

    hr {
        color: #e5e9f2 !important;
        opacity: 1 !important;
    }
}

@media screen and (max-width: 500px) {
    .navbar-nd {
        .container-fluid {
            padding-right: 16px;
            padding-left: 16px;
        }
    }
}

// START: Quick Search Modal
button[data-bs-target="#quickSearchModal"] {
    padding: 12px;
}
#quickSearchModal {
    .navbar-input-column {
        background: transparent;
        border-radius: 0;
        padding: 0 48px;

        &:focus {
            box-shadow: none;
        }
    }

    .tt-menu {
        min-height: 55vh;
    }

    .ic_search {
        left: 12px;
    }

    .clear-field-x {
        right: 12px;
        z-index: 10;
    }

    .ic_search,
    .clear-field-x {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
    }

    .most-searched-course-desktop {
        position: initial;
        top: 0;
        min-height: 55vh;
    }

    .btn-close {
        right: 50px;
        top: 50px;
        filter: invert(99%) sepia(23%) saturate(0%) hue-rotate(217deg)
            brightness(111%) contrast(100%);
    }

    .modal-body {
        min-height: 60vh;
    }
}
// END: Quick Search Modal
