#sidebar___nd.mentor-sidebar {
    top: 30px !important;
    bottom: 30px !important;
    height: calc(100% - 60px);

    @include md {
        margin-left: 30px;
        width: 300px;
    }
}

#navbar_mentor {
    padding: 30px 0;
}

#mentor_dashboard {
    .card-body.with-table {
        padding: 30px;
        padding-top: 40px;
    }
    &.main-wrapper {
        padding-bottom: 100px;
        @include md {
            padding-bottom: 70px;
            margin-left: 380px;
        }
    }

    .benefit {
        .icon {
            margin-right: 10px;
        }
        &:not(:last-child) {
            margin-bottom: 16px;
        }
        &-wrapper {
            @include md {
                margin-left: 26px;
            }
        }
    }

    &.course-detail {
        .benefit-wrapper {
            margin-left: 0;
            @include lg {
                margin-left: 26px;
            }
        }
    }

    #preview {
        height: 120px;
        max-height: 120px;
        min-width: 200px !important;
        width: 200px !important;
        border-radius: 26px;
    }

    .thumbnail-icon {
        padding: 7px;
        width: 38px;
        height: 38px;

        background: $base-grey;
        border-radius: 50%;
        img {
            width: 24px;
            height: 24px;
        }
    }
    &.gate {
        .header-label {
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
        }
        .content {
            padding-top: 80px;

            @include lg {
                max-width: 650px !important;
            }
        }

        .header-primary {
            word-break: break-all;
        }

        .description {
            color: $black;
        }

        .options-wrapper {
            &.single {
                @include lg {
                    .w-50 {
                        max-width: 50%;
                    }
                }
            }
            .w-50 {
                width: 100% !important;

                &:first-child {
                    margin-bottom: 16px;
                }
                @include lg {
                    &:first-child {
                        margin-bottom: 0;
                    }
                }
            }

            .form-select-secondary {
                -moz-appearance: none; /* Firefox */
                -webkit-appearance: none; /* Safari and Chrome */
                appearance: none;

                background-image: url("themes/front/images/svgs/ic_arrow_select.svg");
                background-repeat: no-repeat;
                background-position-x: 95%;
                background-position-y: 20px;
                &::after {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }

            @include lg {
                column-gap: 30px;
            }
        }
    }

    .table {
        img {
            border-radius: 14px;
            width: 115px;
        }
    }

    .dynamic-main-wrapper {
        &.inner- {
            &first {
                margin-left: 30px !important;
            }

            &second {
                margin-left: 60px;
            }
        }
    }

    .dynamic-btn {
        background-color: transparent;
        height: 45px;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        text-decoration-line: underline;

        color: $black;
        border: none;
        outline: none;

        background-image: url("themes/front/images/svgs/dashed-line-large.svg");
        background-size: contain;
        background-repeat: no-repeat;
    }

    .dynamic-input-wrapper {
        margin-bottom: 12px;
        position: relative;
        &.mb-10 {
            margin-bottom: 10px;
        }
        &.mb-20 {
            margin-bottom: 20px;
        }
        &:not(.mb-20):last-child {
            margin-bottom: 24px;
        }
        .dynamic-btn-remove {
            background-color: $base-grey;
            right: 21px;
            border-radius: 50%;
        }

        .form-control {
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            color: $black;

            &::placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;

                color: $black;
            }
            &:-ms-input-placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;

                color: $black;
            }
            &::-ms-input-placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;

                color: $black;
            }
        }
    }

    .btn- {
        &new-course {
            width: 163px !important;
            height: 45px;
            white-space: nowrap;
        }

        &table {
            width: 140px;
            height: 45px;
        }
    }
}
