.mobile-showcase {
    .user-profile {
        .pro-avatar {
            width: min-content;
            border-radius: 100px;
            border: 2.5px solid $blue;
            padding: 10px;
            position: relative;

            .badge-pro {
                border-radius: 100px;
                background-color: $blue;
                width: 54px;
                height: 28px;
                position: absolute;
                bottom: -8px;
                left: 0;
                right: 0;
                margin: 0 auto;

                span {
                    font-weight: 700;
                    font-size: 12px;
                    line-height: 18px;
                    text-align: center;

                    color: $white;
                    text-transform: uppercase;
                }
            }
        }

        img.user-avatar {
            width: 100px !important;
            height: 100px !important;
            object-fit: cover;
        }

        .username {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            color: $black;
        }

        .user-role {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            text-align: center;
            color: $dark-grey;
        }
    }

    .default-card {
        .featured-course {
            position: relative;

            img.cover {
                object-fit: cover;
                border-radius: 16px;
            }
        }
    }
}
