.section-bootcamp {

    .header-title {
        color: $black;
        font-weight: 700;
        font-size: 46px;
        max-width: 585px;
        margin-bottom: 30px;
    }

    div.subtitle {
        font-size: 18px;
        max-width: 585px;
        color: $black;
        line-height: 32px;
    }

    .header-section {
        color: $black;
        font-weight: 700;
        font-size: 22px;
        line-height: 33px;
    }

    .about-bootcamp p {
        line-height: 32px;
        max-width: 530px;
    }

    .bootcamp-thumbnail {
        border-radius: 24px;
    }

    .max-w-550 {
        max-width: 550px;
    }

    .keypoint {
        .item-kp {
            .value {
                font-size: 16px;
                color: $black;
                font-weight: 400;
                line-height: 28px;
            }

            .icon-box {
                float: left;
                margin-right: 12px;
                height: 60px;
            }
        }
    }

    .lets-invest {
        .header-primary {
            max-width: 500px;
            margin: 0 auto;
        }
    }

    .container-fluid {
        padding-right: 50px;
        padding-left: 50px;
        max-width: 1440px;

        .information {
            .item {
                padding: 30px;
                border-radius: 16px;
                background: $white;

                .icon {
                    width: 70px;
                    height: 70px;
                }

                .title {
                    font-weight: 700;
                    font-size: 22px;
                    color: $black;
                }

                .capt {
                    font-size: 16px;
                    font-weight: 400;
                    color: $black;
                    line-height: 30px;
                }
            }
        }

        .custom-accordion-button {
            padding: 20px !important;
        }

        .lessons {
            .accordion-button {
                padding: 30px;
            }

            .accordion-body {
                padding: 0 30px 30px;
            }

            .number {
                width: 50px;
                height: 50px;
                background: $dark-blue;
                border-radius: 100px;
                float: left;
                margin-right: 16px;

                p {
                    font-weight: 700;
                    color: $black;
                    font-size: 20px;
                    padding: 12px;
                }
            }

            .title {
                font-size: 20px;
                font-weight: 700;
                color: $black;
                float: left;
                margin-top: 14px;
            }

            .materi {
                .btn-preview {
                    .material-icons {
                        color: $black;
                    }
                }

                .video-title {
                    font-weight: 500;
                    color: $black;
                    font-size: 16px;
                    float: left;
                }

                .material-icons {
                    color: $black;
                    float: left;
                    margin: 0 12px;
                }

                .min {
                    font-weight: 500;
                    float: right;
                    color: $black;
                    font-size: 16px;
                }
            }
        }

        .faq {
            margin: 100px 0;
        }

        .faq,
        .lessons {
            .accord-content {
                .accordion-item {
                    margin-bottom: 22px;
                    border: none;
                    border-radius: 16px;

                    .accordion-collapse {
                        border: 0;
                        background: $white;
                        border-bottom-right-radius: 16px;
                        border-bottom-left-radius: 16px;
                    }

                    .accordion-body {
                        line-height: 28px;
                        color: $black;
                    }

                    .accordion-button {
                        background: $white;
                        border: 0;
                        color: $black;
                        font-size: 16px;
                        font-weight: 700;
                        border-top-right-radius: 16px;
                        border-top-left-radius: 16px;
                    }

                    .accordion-button:not(.collapsed) {
                        box-shadow: none;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 990px) {
    .section-bootcamp {
        .img-fluid {
            margin: 30px 0;
        }

        .btn-bootcamp {
            width: 100%;
        }
    }
}

@media screen and (max-width: 500px) {
    .section-bootcamp {
        .container-fluid {
            padding-right: 12px;
            padding-left: 12px;

            .header-title {
                font-size: 36px !important;
            }

            .information {
                .item {
                    padding: 24px;
                }
            }
        }
    }
}
