.recommendation-card {
    column-gap: 18px;
    max-width: 482px;
    font-family: "Poppins";

    &- {
        &row {
            row-gap: 20px;
            padding-bottom: 150px;
        }

        &title {
            font-weight: 700;
            font-size: 22px;
            line-height: 27px;

            color: $black;
            margin-top: 30px !important;
        }
    }

    .thumbnail-course {
        position: relative;
        > img {
            min-width: 160px !important;
            width: 160px !important;
            height: 110px !important;
            object-fit: cover;
            border-radius: 20px;
        }

        .labelJoinedCourse {
            margin: 0;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
        }
    }

    .course- {
        &content {
            row-gap: 16px;
            min-height: 110px;
            padding: 8px 0;
        }

        &name {
            color: $black;
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;

            position: relative;
            z-index: 3;
        }
    }

    .star-rating {
        column-gap: 4px;
        img {
            width: 21px;
            height: 21px;
        }
        span {
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            color: $black;
            margin-top: 2px !important;
        }

        &.hide-rating {
            img {
                display: none !important;
            }
        }
    }
}
