section#roadmapCareer, #mobileRoadmapCareer {
    @media screen and (min-width: 576px) {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .roadmapCard {
        position: relative;
        padding: 30px;
        border-radius: 16px;
        border: none;
        background: $white;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        color: $black;
        height: 100%;
        box-shadow: 0px 9px 30px rgba(234, 236, 240, 0.5);

        img.thumbnail {
            border-radius: 16px;
            object-fit: cover;
            width: 100%;
            min-height: 200px;
            max-height: 200px;
        }

        .title {
            margin-top: 30px;
            margin-bottom: 6px;
            font-size: 20px;
            line-height: 30px;
            font-weight: 700;
            min-height: 60px;
        }

        .subtitle {
            color: $dark-grey;
            font-size: 16px;
            font-weight: 400;
            margin-bottom: 16px;
        }

        .roadmap-tools {
            display: flex;
            align-items: center;
            gap: 12px;

            img {
                height: 36px;
                width: auto;
                object-fit: cover;
            }
        }

        @media screen and (max-width: 575px) {
            padding: 12px 12px 14px;
            width: 265px;

            img.thumbnail {
                min-height: 150px;
                max-height: 150px;
            }

            .title {
                margin-top: 16px;
                margin-bottom: 4px;
                font-size: 16px;
                line-height: 24px;
                min-height: 48px;
            }

            .subtitle {
                font-size: 14px;
                margin-bottom: 14px;
            }

            .roadmap-tools {
                gap: 8px;

                img {
                    height: 25px;
                }
            }
        }
    }
}
