main.browseKelas {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    padding: 80px 0 50px;

    .front-text-group {
        gap: 0;

        .header-primary {
            font-size: 38px;
            line-height: 57px;
        }

        p.capt {
            margin-top: 10px;
            font-size: 18px;
        }
    }

    .primary-title {
        margin-bottom: 16px;
    }

    #catalogue-courses-v2 {
        .course-filter {
            padding: 0 12px !important;
        }
    }

    .skeleton-card {
        padding: 0;

        .img-skeleton {
            border-radius: 16px 16px 0 0 !important;
        }

        .detail {
            padding: 0 16px;
        }

        .skeleton-footer {
            padding: 0 16px 16px;
        }
    }
}

// Detail kelas desktop
.course-details {
    &.desktop {
        #player {
            @include lg {
                iframe {
                    transition: top 0.5s, left 0.5s, opacity 1s !important;
                }
                position: relative;
                top: 0px !important;
                &.sticky-custom {
                    &.show-sticky {
                        iframe {
                            opacity: 0;
                            position: fixed;
                            z-index: 9999;
                            width: 470px !important;
                            height: 265px !important;
                            border-radius: 10px;
                            top: calc(100vh - 265px - 30px);
                            left: calc(100vw - 470px - 30px) !important;
                        }

                        &.animate-sticky {
                            iframe {
                                opacity: 1;
                            }
                        }
                    }
                }

                &.sticky-custom.show-sticky.adminMode iframe {
                    top: calc(100vh - 265px - 120px) !important;
                }
            }
        }
    }

    #userReviewsContainer {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: flex-start;
        max-width: 746px + 24px;

        #reviewsContentData {
            gap: 20px;
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 1fr));
            width: 100%;
        }
        #filterReviews {
            .btn-secondary {
                padding: 6px 14px;
                display: flex;
                align-items: center;
                gap: 4px;

                img {
                    width: 18px;
                    height: 18px;
                }

                &:focus,
                &:hover {
                    outline: none !important;
                    box-shadow: none !important;
                    border: none !important;
                }

                p {
                    margin-bottom: 0;
                    margin-top: 1px;
                }
            }
        }
    }

    #pills-about #courseAboutDesc p:last-child {
        margin-bottom: 0;
    }
}

#course_detail_btn {
    &.btn-to-top {
        right: 25px;
        bottom: calc(265px + 30px + 50px);
        @media screen and (max-width: 575px) {
            right: 12px;
            bottom: 100px;
        }
    }
}
