#event-page {
    padding-bottom: 100px;
    .btn-event {
        @media screen and (max-width: 991px) {
            width: 100% !important;
        }
        white-space: nowrap;
        width: 170px !important;
        padding: 10px 17px !important;
    }
    .desktop-only {
        display: none;
        @media screen and (min-width: 991px) {
            display: block;
        }
    }
    .event {
        &- {
            &title {
                font-weight: 700;
                font-size: 46px;
                line-height: 69px;
                text-align: center;
                color: $black;
                margin-top: 55px;
                margin-bottom: 50px;
                div {
                    color: $blue;
                }
            }
            &description {
                font-weight: 400;
                font-size: 18px;
                line-height: 32px;
                /* or 178% */
                text-align: center;
                color: $black;
                margin-top: 45px;
                margin-bottom: 30px;
            }
            &image {
                border-radius: 26px;
            }
        }
    }
}