body .course-playing .quiz-mobile-section {

    .fs-7 {
        font-size: 14px;
    }

    a.d-disabled {
        pointer-events: none;
        cursor: default;
    }

    .quiz-question-title {
        font-size: 30px;
        text-align: center;
        line-height: 1.6;
    }

    .option-quiz {
        font-size: 20px;
        margin: 0;
    }

    .quiz-option-item {
        padding: 14px;
        border: 1px solid #e5e5e5;
        margin: 20px;
        border-radius: 8px;
        transition: all 0.3s;

        &:hover {
            background: #f0f0f0;
        }
    }


    .item-optiom {
        text-decoration: none !important;
    }

    @media (max-width: 767.98px) {

        .quiz-mobile-section {
            margin-left: 0 !important;
            padding: 0 !important;

            .container {
                min-width: 0 !important;
                padding: 0 !important;

                .body-welcome {
                    margin: 50px;
                }
            }
        }

        .dropload {
            min-height: 282px;
        }
    }

}

body.dark {

    .quiz-question-title,
    p {
        color: #f6f8fd !important;
    }

    .quiz-option-item:hover {
        color: #34364a;
    }
}
