html,
body {
    &.disable-scroll {
        overflow: hidden !important;
    }

    &.set-height {
        height: 100vh !important;
    }
}

@media screen and (max-width: 575px) {
    html,
    body {
        overflow-x: hidden;
        font-family: "Poppins", sans-serif;
    }

    .--bwa-mobile-version {
        // Default black color
        color: $black;

        .container {
            padding-left: 1rem;
            padding-right: 1rem;
        }

        img {
            font-size: 12px;
        }

        .offcanvas-backdrop.fade.show {
            // background: rgba(6, 9, 28, 0.8);
            background-color: #06091c;
            opacity: 0.8;
        }

        .btn-plain {
            outline: none;
            border: none;
            position: relative;
            z-index: 1;
        }

        .link-basic {
            text-decoration: none;
            color: currentColor;

            &:hover {
                text-decoration: none;
                color: currentColor;
            }
        }

        .min-w-max-content {
            min-width: max-content;
        }

        .mobile-screen-cover {
            background: rgba(6, 9, 28, 0.8);
            z-index: 2;
        }

        .search-input-home {
            color: $dark-grey;
            font-weight: 400;
            background-color: $white !important;
            background-repeat: no-repeat !important;
            background-position: calc(100% - 20px) !important;
        }

        .section-searchbar {
            margin-bottom: 20px;
        }

        .mobile_card {
            padding: 12px;
            background: $white;
            border-radius: 16px;
            position: relative;

            display: flex;
            flex-direction: column;
        }

        .__user_details {
            display: flex;
            gap: 12px;
            align-items: center;
            margin-bottom: 16px;
            color: $black;

            img {
                width: 50px;
                height: 50px;
                border-radius: 999px;
                object-fit: cover;
            }

            .primary-text {
                font-weight: 700;
                font-size: 16px;
                margin-bottom: 2px;
                word-break: break-word;
                max-width: 100%;
            }

            .secondary-text {
                color: $dark-grey;
                word-break: break-word;
                font-size: 14px;
            }
        }

        .rating_component {
            display: flex;
            gap: 4px;
            align-items: center;

            img {
                width: 20px;
            }
        }

        .top-featured-section {
            padding: 0 16px;

            .__most-up-to-date {
                background-color: transparent;
                border-radius: 0;
                width: max-content;
                max-height: initial;
                display: grid;
                grid-template-columns: repeat(3, minmax(0, 1fr));
                column-gap: 18px;
                row-gap: 16px;

                .course-content {
                    background-color: $white;
                    border-radius: 16px;
                    padding: 12px !important;
                    width: 290px;
                    display: flex;
                    align-items: center;
                    gap: 12px;
                    position: relative;

                    .price {
                        column-gap: 5px;
                    }

                    .thumbnail {
                        width: 78px;
                        height: 60px;
                        border-radius: 14px;
                        object-fit: cover;
                    }

                    .course-title {
                        margin-bottom: 0;
                        line-height: 21px;
                        font-weight: 700;
                        font-size: 14px;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        word-wrap: break-word;
                        max-width: 170px;
                        height: auto;
                    }

                    .price,
                    .disc-price,
                    .promo-price {
                        font-weight: 500;
                        color: $black;
                        font-size: 12px;
                    }

                    .labelJoinedCourse {
                        p.__text {
                            font-size: 10px;
                        }

                        > img {
                            width: 16px;
                            height: 16px;
                        }
                    }
                }
            }
        }

        ul {
            padding: 0;
            margin: 0;
        }

        .mobile_list_points {
            display: flex;
            flex-direction: column;
            gap: 6px;

            .list {
                display: flex;
                flex-direction: row;
                gap: 8px;
                align-items: flex-start;

                span {
                    font-size: 14px;
                    line-height: 26px;
                }
            }
        }

        .headline_page {
            margin-bottom: 16px;

            .tagline {
                margin-bottom: 0;
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
            }

            .primary-label {
                font-weight: 500;
                font-size: 14px;
                line-height: 21px;
                margin-bottom: 4px;
                color: $primary-green;
            }
        }

        .section-title {
            font-weight: 700;
            font-size: 16px;
            margin-bottom: 6px;
        }

        .mobile_card_is_selected,
        #checkoutMobileTab .nav-link.active {
            border: 2px solid $primary-blue;
            -webkit-border: 2px solid $primary-blue;
            -moz-border: 2px solid $primary-blue;
            transition: all 0.2s ease !important;
        }

        .nav-tabs {
            border-bottom: 0;
        }

        @import "mobile-components/checkout-kelas";

        @import "pages/journey";
        @import "pages/detail-kelas";
        @import "pages/handbook";
        @import "pages/sale";
        @import "pages/transactions";
        @import "pages/portfolio";
    }

    @import "./mobile-components/navbar";
    @import "./mobile-components/hero";
    @import "./mobile-components/roadmap";
    @import "./mobile-components/review";
    @import "./mobile-components/course-categories";
    @import "./mobile-components/advice-card";
    @import "./mobile-components/footer-mobile";
    @import "./mobile-components/swiper-mobile";
    @import "./mobile-components/start-building";
    @import "./mobile-components/course-card";
    @import "./mobile-components/mobile-search-module";
    @import "./mobile-components/mobile-basic";
    @import "mobile-components/tabs-content-nd";
    @import "mobile-components/course-accordion";
    @import "mobile-components/auth-pieces";
    @import "mobile-components/_course-card-responsive";
}
