.course-filter, .filter-catalogue {
    .item-filter {
        display: flex;
        flex-direction: column;
        gap: 16px;

        .head {
            color: $black;
            font-weight: bold;
            font-size: 20px;
            line-height: 30px;
        }

        .child {
            display: flex;
            align-items: center;
            cursor: pointer !important;

            input,
            label {
                cursor: pointer !important;
            }

            label {
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: $black;
            }

            @media screen and (max-width: 991px) {
                .nd-checkbox {
                    width: 20px;
                    height: 20px;
                    float: left;
                    margin-right: 10px;
                    border-radius: 16px;
                }
            }
        }
    }

    // Checkbox styling
    .container-inp {
        display: block;
        position: relative;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        border-radius: 6px;

        input {
            appearance: none;
            margin: 0;
            width: 24px;
            height: 24px;
            border: 1px solid #000000;
            border-radius: 6px;
            display: grid;
            place-content: center;
            margin-right: 10px;
        }

        input::before {
            content: "";
            transform: scale(0);
            transition: 120ms transform ease-in-out;
            box-shadow: inset 1em 1em var(--form-control-color);
            background-color: $blue;

            width: 18px;
            height: 18px;
            border-radius: 6px;
        }

        input:checked::before {
            transform: scale(1);
        }
    }
}


// Mobile filter
@media screen and (max-width: 991px) {
    .filter-catalogue {
        .options {
            position: fixed;
            overflow-y: auto;
            z-index: 600;
            padding: 20px;
            top: 0;
            background: $white;
            bottom: 0;
            left: 0;
            width: 100%;
        }

        .btn-filter {
            position: fixed;
            z-index: 100;
            left: 0;
            right: 0;
            bottom: 30px;
            width: max-content !important;
            margin: 0 auto;
        }
    }
}
