.--bwa-mobile-version {
    #navbar-mobile {
        height: 90px;
        padding: 0 16px;

        &.simple-version {
            height: 56px !important;
        }

        .navbar {
            &- {
                &burger-btn {
                    background-color: $base-grey;
                    padding: 12px 11px;
                    border-radius: 12px;
                    margin-right: 12px;

                    &.simple {
                        width: 36px;
                        height: 36px;
                    }
                }
                &search-btn {
                    background-color: $base-grey;
                    width: 36px;
                    height: 36px;

                    img {
                        width: 20px;
                        height: 20px;
                    }
                }
                &auth {
                    background: #e5e9f2;
                    border-radius: 100px;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 24px;
                    text-align: center;
                    color: #34364a;
                    padding: 6px 21px;
                }
                &thumbnail {
                    margin-left: 12px;

                    img {
                        object-fit: cover;
                        width: 36px;
                        height: 36px;
                    }
                }
            }
        }
    }
    .mobile-navbar-offcanvas {
        height: fit-content !important;
        min-height: fit-content !important;
        bottom: 0 !important;
        transition: all 0.3s;
        // &.extended {
        //     height: 80vh !important;
        // }
        border-radius: 35px 35px 0 0;
        @media screen and (min-width: 330px) {
            border-radius: 50px 50px 0 0;
        }
        .content-menu {
            display: flex;
            flex-direction: column;
            row-gap: 20px;

            // height: calc(50vh - 86px);
            // overflow: hidden;

            // &.extended {
            //     overflow: scroll;
            // }
            // &.extended-visible {
            //     overflow: visible;
            // }
            // &.set-height {
            //     height: fit-content;

            // }
        }
    }
    .mobile-auth-navbar-offcanvas {
        height: fit-content !important;
        bottom: 0 !important;
        transition: all 0.3s;
        border-radius: 35px 35px 0 0;
        overflow: hidden;
        @media screen and (min-width: 330px) {
            border-radius: 50px 50px 0 0;
        }
        .offcanvas-body {
            overflow-y: scroll;
            padding: 30px 24px 40px 24px;
            -ms-overflow-style: none;
            /* Internet Explorer 10+ */
            scrollbar-width: none;
            /* Firefox */
            &::-webkit-scrollbar {
                display: none;
                /* Safari and Chrome */
            }
        }
        .thumbnail {
            object-fit: cover;
            width: 70px;
            min-width: 70px;
            height: 70px;
            margin-right: 12px;
        }
        .username {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: $black;
            margin-bottom: 0;
        }
        .role {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: $dark-grey;
            margin-bottom: 0;
        }
        .badge {
            font-weight: 700;
            font-size: 12px;
            line-height: 18px;
            text-align: center;
            color: $white;
            background: $primary-blue;
            border-radius: 100px;
            width: 54px;
            height: 28px;

            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }
        .auth-link {
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            color: $black;
            margin-bottom: 16px;
        }
        .check-link {
            font-weight: 500;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: $black;
            width: 90px;
            height: 36px;
            background: $base-grey;
            border-radius: 100px;
            margin-top: 27px;
        }

        .bg-primary-blue {
            background: $primary-blue;
        }

        .badge-circle {
            font-weight: 700;
            font-size: 8px;
            line-height: 16px;

            color: $white;
            width: 16px;
            height: 16px;
            margin-left: 6px;
        }
    }
    .mobile-menu {
        min-height: fit-content !important;
        max-height: 100vh !important;
        z-index: 3;
        padding: 36px 24px 50px 24px;
        overflow-x: hidden;
        overflow-y: scroll;
        // overflow-y: scroll;
        bottom: -100vh;
        -ms-overflow-style: none;
        /* Internet Explorer 10+ */
        scrollbar-width: none;
        /* Firefox */

        &::-webkit-scrollbar {
            display: none;
            /* Safari and Chrome */
        }
        &.is-open {
            bottom: 0;
        }
        &- {
            &close {
                background-color: #fff;
                /* top: 36px;
                    right: 46px; */
                z-index: 5;
                margin-right: 16px;
            }
            &title {
                font-weight: 600;
                font-size: 14px;
                line-height: 21px;
                color: $black;
                margin-bottom: 10px;
                &:not(:first-child) {
                    margin-top: 20px;
                }
            }
            &item {
                border: 1px solid #e5e9f2;
                border-radius: 22px;
                width: 100%;
                max-width: 80px;
                padding: 16px;
                // margin-bottom: 20px;
                /* margin-right: 14px; */
                width: 100px !important;
                min-width: 100px !important;
                max-width: 100px !important;
                height: 110px !important;
            }
            &thumbnail {
                width: 50px;
                height: 50px;
                margin-bottom: 7px;
            }
            &name {
                font-weight: 400;
                line-height: 21px;
                text-align: center;
                color: #34364a;
                font-size: 14px;
            }
        }
    }
}
