#bwavsothers-table {
    table {
        z-index: 1;
    }
    .tl-rounded {
        border-top-left-radius: 20px;
    }
    .tr-rounded {
        border-top-right-radius: 20px;
    }
    thead {
        z-index: 4;
        color: #34364a;
        tr {
            th:nth-child(2) {
                padding: 0;
                padding-left: 6%;
            }
            th:last-of-type {
                padding: 0;
                padding-right: 6%;
            }
        }
        h2 {
            font-weight: 700;
            font-size: 26px;
            line-height: 39px;
        }
        span {
            font-weight: 300;
            font-size: 16px;
            line-height: 24px;
        }
        .logo-div {
            height: 247.5px;
        }
        .title-div {
            height: fit-content;
            padding-inline: 30px;
            padding-top: 30px;
            padding-bottom: 20.5px;
        }
    }

    tbody {
        top: -87px;
        z-index: 2;
        color: #34364a;
        .show-sticky {
            th {
                padding: 0 !important;
            }
        }
        tr {
            background-color: white;
            height: 87px !important;
            th {
                padding-inline: 30px !important;
                padding-block: 20.5;
                font-weight: 600;
                font-size: 18px;
                line-height: 24px;
            }
            td {
                font-weight: 600;
                font-size: 18px;
                line-height: 24px;
            }
            td:first-of-type {
                padding: 0;
                padding-left: 6%;
            }
            td:last-of-type {
                padding: 0;
                padding-right: 6%;
            }
        }
        tr:not(:first-child) {
            th {
                height: inherit;
                display: flex;
                flex-direction: row;
                padding-block: 8px;
            }
            p {
                text-align: start;
                align-self: center;
            }
        }
        tr:last-of-type {
            background-color: transparent !important;
            th {
                border-bottom-left-radius: 20px;
                background-color: white;
            }
            td {
                background-color: white;
            }
            td:last-of-type {
                border-bottom-right-radius: 20px;
                background-color: white;
            }
        }
    }

    .icon-th {
        margin-right: 16px;
    }
    thead p,
    tbody p {
        margin-block: 0;
        text-align: center;
        color: #34364a;
    }
    .vs {
        font-weight: 600;
        font-size: 32px;
        line-height: 48px;
    }
    .platforms {
        font-weight: 700;
        font-size: 26px;
        line-height: 39px;
    }
    // .sticky-check {
    //     // top: -1px;
    //     height: 247.5px;
    // }
    .sticky-active {
        min-height: 87px !important;
        background-color: white;
        overflow-x: clip;
        div {
            min-height: inherit;
            box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 23px 1px;
        }
        .logo-div {
            padding-left: 25%;
            img {
                width: 50px !important;
            }
        }
        .platforms {
            padding-right: 25%;
        }
        .vs,
        .platforms {
            font-size: 16px !important;
            line-height: 24px !important;
            align-self: center;
        }
    }
}

@media only screen and (max-width: 767px) {
    thead {
        .logo-div img {
            width: 50px !important;
        }
        .vs,
        .platforms {
            font-size: 16px !important;
            line-height: 24px !important;
            align-self: center;
        }
    }
    tbody {
        .icon-th {
            width: 30px;
        }
    }
}

@media only screen and (max-width: 575px) {
    thead {
        .title-div {
            h2 {
                font-size: 20px !important;
            }
            span {
                font-size: 14px !important;
            }
        }

        .logo-div img {
            width: 50px !important;
        }
        .vs,
        .platforms {
            font-size: 14px !important;
            line-height: 24px !important;
            align-self: center;
        }
    }
    tbody {
        .icon-th {
            width: 25px !important;
        }
        tr:not(:first-of-type) {
            th {
                font-size: 14px !important;
            }
            td {
                font-size: 12px !important;
                img {
                    width: 18px !important;
                }
            }
        }
    }
}
