.gate {
    .__authentication-banner {
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;

        img {
            object-fit: cover;
            width: 470px;
            min-height: 100vh;
        }
    }
    .place-photo {
        width: 100px;
        height: 100px;
        background: $dark-blue;
        border-radius: 100px;
        margin: 0 auto;

        .icon {
            margin-top: 30px;
        }
    }

    .name {
        font-size: 20px;
        color: $black;
        font-weight: 700;
    }

    .username {
        font-weight: 400;
        font-size: 16px;
        color: $dark-grey;
    }

    .showcase {
        .banner {
            min-height: 100vh;
            width: 470px;
            object-fit: cover;
        }
    }

    .content {
        @media screen and (max-width: 999px) {
            padding-top: 50px;
        }
        @media screen and (min-width: 1000px) {
            padding-top: 80px;
            width: 100%;
            max-width: 400px;
        }

        .logo {
            height: 50px;
            width: 50px;
        }

        .form-group {
            background: $white;
            padding: 30px;
            border-radius: 16px;

            .divider {
                height: 1px;
                background-color: $dark-purple !important;
                margin-top: 30px;
                margin-bottom: 30px;
            }

            label {
                color: $black;
                font-size: 16px;
                font-weight: 400;
            }

            button {
                min-width: 100%;
                height: 45px;
            }

            .btn-google {
                min-width: 100%;
                background: $black;
                min-height: 45px;
                border-radius: 100px;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: $white;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 26px;
                    height: 26px;
                    margin-right: 8px;
                }
            }

            input {
                border-radius: 100px;
                background: $dark-blue;
                border: 0;
                height: 45px;
                padding-left: 20px;

                &:focus {
                    border: 2px solid $high-dark-blue;
                }
            }
        }
    }
}

.revealPassword {
    position: relative;

    input[type="password"] {
        &::-ms-reveal {
            display: none;
            content: "";
            opacity: 0;
        }
    }

    div {
        position: absolute;
        z-index: 10;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        width: fit-content;

        img {
            width: 20px;
            height: 20px;
        }
    }
}
