.video-iframe {
    border-radius: 16px;
    transition: all 0.3s;
}

.embed-responsive {
    position: relative;
    display: block;
    width: 100%;
    height: 80vh;
    padding: 0;
    overflow: hidden;

    &::before {
        display: block;
        content: "";
    }

    @media screen and (max-width: 500px) {
        height: auto;
    }

    .embed-responsive-item,
    iframe,
    embed,
    object,
    video {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 0;
    }

    .img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .img__cover {
        object-fit: cover;
    }

    .img__contain {
        object-fit: contain;
    }
}

.embed-responsive-21by9::before {
    padding-top: 42.8571428571%;
}

.embed-responsive-16by9::before {
    padding-top: 56.25%;
}

.embed-responsive-4by3::before {
    padding-top: 75%;
}

.embed-responsive-1by1::before {
    padding-top: 100%;
}
