#portfolio_page {
    .user-avatar {
        width: 100px !important;
        min-width: 100px !important;
        height: 100px !important;
        margin-right: 0;
    }
    .portfolio- {
        &title {
            font-weight: 700;
            font-size: 20px;
            line-height: 30px;
            text-align: center;

            color: $black;
            margin-bottom: 6px;
        }

        &icon {
            width: 180px;
            height: 180px;
        }

        &description {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            text-align: center;

            color: $black;
        }

        &card {
            width: 100%;
            padding: 12px;
            border-radius: 16px;
        }
        &thumbnail {
            object-position: center;
            object-fit: cover;
            border-radius: 16px;
            width: 100%;
            height: 100%;
        }

        &col {
            .btn {
                font-weight: 500;
                font-size: 14px;
                padding: 6px 14px;
                line-height: 24px;
                width: 48%;
            }
            .btn- {
                &profile {
                    width: 110px !important;
                }

                &add-new {
                    width: 120px !important;
                }
            }
        }
    }

    #talentTab {
        margin-bottom: 16px;

        & {
            -ms-overflow-style: none; /* Internet Explorer 10+ */
            scrollbar-width: none; /* Firefox */
        }
        &::-webkit-scrollbar {
            display: none; /* Safari and Chrome */
        }

        overflow-y: hidden !important;

        .nav- {
            &item {
                &:not(:last-child) {
                    padding-right: 14px;
                }

                &:first-child {
                    margin-left: 16px;
                }

                &:last-child {
                    margin-right: 16px;
                }
            }
            &link {
                font-weight: 500;
                font-size: 14px;
                line-height: 24px;
                text-align: center;

                width: 136px;
                height: 36px;
                border-radius: 100px;

                background-color: $base-grey;
                color: $black;

                white-space: nowrap;
                display: flex;
                justify-content: center;
                align-items: center;
                &.active {
                    background-color: $black;
                    color: $white;
                }
            }
        }
    }
}
