.tt-menu,
.most-searched-course,
.most-searched-course-desktop {
    background-color: $white;
    border-radius: 8px;
    position: absolute;
    top: 100px;
    width: 100%;
    max-height: 340px;
    overflow-y: auto;
    z-index: 500;

    &::-webkit-scrollbar {
        width: 0;
        display: none;
    }
    @include md {
        &::-webkit-scrollbar {
            width: 6px;
            display: block;
        }
    }

    &::-webkit-scrollbar-thumb {
        background: #e5e9f2;
        border-radius: 10px;
    }

    .course-content {
        display: flex;
        align-items: center;
        gap: 14px;
        padding: 10px;
        border-bottom: 1px solid $soft-blue;
        position: relative;
        cursor: pointer;
        transition: 0.1s ease-in-out;

        &:hover,
        &.tt-cursor {
            background: $soft-blue;
        }

        .thumbnail {
            width: 120px;
            height: 80px;

            img {
                border-radius: 8px;
                object-fit: cover;
                width: inherit;
                height: 100%;
            }

            &.skeleton {
                @extend .skeleton-animation;
                min-width: 120px;
            }
        }

        .course-info {
            width: 100%;
            max-width: 100%;
        }

        .course-title {
            font-weight: 700;
            color: $black;
            font-size: 16px;
            max-width: 333px;
            line-height: 24px;
            margin-bottom: 8px;

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;

            @media screen and (max-width: 500px) {
                font-size: 14px;
            }

            &.skeleton {
                @extend .skeleton-animation;
                width: 300px;
                height: 24px;

                &.navbar {
                    width: 100%;
                }

                @media screen and (max-width: 500px) {
                    width: 150px;
                }
            }
        }

        .price {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 14px;
            color: $dark-grey;
            font-weight: 400;

            @media screen and (max-width: 500px) {
                font-size: 12px;
            }

            &.skeleton {
                @extend .skeleton-animation;
                width: 220px;
                height: 20px;

                &.navbar {
                    width: 75%;
                }

                @media screen and (max-width: 500px) {
                    width: 80px;
                }
            }
        }

        .labelJoinedCourse {
            img {
                width: 20px;
                height: 20px;
            }

            .__text {
                font-size: 12px;
                line-height: 16px;
            }
            padding: 4px 12px !important;
        }
    }

    a.link {
        padding: 10px;
        color: $black;
        font-size: 14px;
        display: block;
        text-align: center;
    }
}

.twitter-typeahead,
.tt-hint,
.tt-input,
.tt-menu {
    width: 100%;
}
